  "DOTA_Tooltip_ability_lina_laguna_blade": "Laguna Blade",
  "DOTA_Tooltip_ability_lina_laguna_blade_Description": "Fires off a bolt of lightning at a single enemy unit, dealing massive damage.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_lina_laguna_blade_Lore": "The air around Lina becomes so intensely torrid that it scorches a foe that comes too close with white-hot lightning.",
