  "DOTA_Tooltip_ability_mirana_arrow": "Sacred Arrow",
  "DOTA_Tooltip_ability_mirana_arrow_Description": "Fires a long-range arrow with deadly precision, which stuns and damages the first enemy unit it strikes. The stun duration ranges from 0.01 to %arrow_max_stun% seconds, with bonus damage up to %arrow_bonus_damage% added, based on the distance the arrow travels to its target. Instantly kills the first non-ancient creep it hits.",
  "DOTA_Tooltip_ability_mirana_arrow_Lore": "Lunar energies make the Princess of the Moon's arrows more deadly than most.",
  "DOTA_Tooltip_ability_mirana_arrow_Note0": "For every 150 distance the arrow travels, the stun duration upon impact increases by 0.5 seconds, and the bonus damage added increases by 14.",
