  "DOTA_Tooltip_ability_mirana_leap_Description": "Mirana leaps forward into battle, empowering herself with a ferocious roar that grants bonus attack and movement speed. Speed bonus lasts %leap_bonus_duration% seconds.",
  "DOTA_Tooltip_ability_mirana_leap_Lore": "None can fail to recognize Mirana's iconic white beast in the heat of battle.",
