  "DOTA_Tooltip_ability_life_stealer_open_wounds": "Open Wounds",
  "DOTA_Tooltip_ability_life_stealer_open_wounds_Description": "Lifestealer rends an enemy unit, slowing the victim's movement speed and allowing all allies to regain health for a percentage of the damage they deal to that unit. All damage dealt will steal life, including damage from spells. The victim recovers movement speed over the duration.",
  "DOTA_Tooltip_ability_life_stealer_open_wounds_Lore": "Fresh wounds and the scent of blood often draw out the scavengers to finish the job.",
  "DOTA_Tooltip_ability_life_stealer_open_wounds_Note0": "Open Wounds slows over 8 different steps: 70% / 70% / 60% / 50% / 30% / 10% / 10% / 10%, with the slow decreasing one step per second.",
  "DOTA_Tooltip_ability_life_stealer_open_wounds_Note1": "Stacks additively with other sources of Lifesteal.",
