  "DOTA_Tooltip_ability_life_stealer_infest_Lore": "With this infestation, N'aix consumes the life blood of the host, restoring him back to full unholy power.",
  "DOTA_Tooltip_ability_life_stealer_infest_Description": "Lifestealer infests the body of a target unit, becoming undetectable inside. He can then explode from the host body, dealing damage to nearby enemies. If the infested unit is an enemy creep or a neutral creep, he can take control of the unit's ability to move and attack, and when consumed the creep will heal Lifestealer's health equal to the creep's current health.  Does not work on enemy heroes.\\n\\nDISPEL TYPE: Basic Dispel",
  "DOTA_Tooltip_ability_life_stealer_infest_Note0": "If the infested unit dies before Lifestealer consumes it, no health is gained.",
  "DOTA_Tooltip_ability_life_stealer_infest_Note1": "Lifestealer gains experience while under the effects of Infest.",
  "DOTA_Tooltip_ability_life_stealer_infest_Note2": "Creeps that are controlled while Infested share Lifestealer's movement speed.",
  "DOTA_Tooltip_ability_life_stealer_infest_Note3": "Infest Level 2 and 3 can be used on Ancient creeps.",
