  "DOTA_Tooltip_ability_life_stealer_assimilate_Description": "Lifestealer swallows an allied hero, allowing them to hide within him. Any healing Lifestealer receives is shared with the hero within. When an assimilated hero leaves Lifestealer, they erupt outward, damaging nearby enemies.\\n\\nDISPEL TYPE: Strong Dispel",
