  "DOTA_Tooltip_ability_furion_teleportation_Lore": "The Prophet keeps his sentinel over the forest, protecting it when in need.",
  "DOTA_Tooltip_ability_furion_teleportation_Note0": "Teleport takes 3 seconds to cast.",
