  "DOTA_Tooltip_ability_furion_teleportation_Note2": "When the cast begins, an effect is created at the target location, which is visible to allies and enemies.",
  "DOTA_Tooltip_ability_furion_force_of_nature": "Nature's Call",
  "DOTA_Tooltip_ability_furion_force_of_nature_Description": "Converts an area of trees into Treants. Treants have %treant_health_tooltip% health and deal %treant_min_dmg_tooltip%-%treant_max_dmg_tooltip% damage.",
  "DOTA_Tooltip_ability_furion_force_of_nature_Lore": "Verodicia bestowed the Prophet with the ability to summon the Treant Guard, a mythical sentient army of the wild.",
