  "DOTA_Tooltip_ability_furion_wrath_of_nature": "Wrath of Nature",
  "DOTA_Tooltip_ability_furion_wrath_of_nature_Description": "Damaging energy bounces around the map, striking enemies close to the cast point that are in vision. Each enemy hit beyond the first adds damage.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_furion_wrath_of_nature_Lore": "Nature's Prophet calls Verodicia's fury to strike down those who might ravage the wilderness.",
  "DOTA_Tooltip_ability_furion_wrath_of_nature_Note0": "Wrath of Nature will not hit units out of vision.",
  "DOTA_Tooltip_ability_furion_wrath_of_nature_Note1": "Can be cast through the minimap; the nearest valid target will be selected.",
  "DOTA_Tooltip_ability_furion_wrath_of_nature_max_targets": "MAX TARGETS:",
