  "DOTA_Tooltip_ability_furion_wrath_of_nature_aghanim_description": "Increases damage. If a unit is killed by Wrath of Nature, or within 4 seconds thereafter, a Nature's Call treant will spawn. These treants' health and damage are based on the current level of Nature's Call. If a hero is killed, a Greater Treant will be created, which has 3 times as much health and damage.",
  "DOTA_Tooltip_ability_windrunner_focusfire": "Focus Fire",
  "DOTA_Tooltip_ability_windrunner_focusfire_Description": "Windranger channels the wind to gain %bonus_attack_speed% additional attack speed against a single enemy unit or structure, though with a reduction to her attack damage.  Extra damage from secondary item effects is not reduced.  Lasts %focusfire_duration_tooltip% seconds.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_windrunner_focusfire_Lore": "Lyralei's ability to bombard opponents with a flurry of arrows is unparalleled - even at the expense of accuracy.",
  "DOTA_Tooltip_ability_windrunner_focusfire_Note0": "Can target buildings and Spell Immune units.",
  "DOTA_Tooltip_ability_windrunner_focusfire_bonus_attack_speed": "BONUS ATTACK SPEED:",
