  "DOTA_Tooltip_ability_windrunner_powershot": "Powershot",
  "DOTA_Tooltip_ability_windrunner_powershot_Description": "Windranger charges her bow for up to 1 second for a single powerful shot, which deals more damage the longer it is charged. The arrow damages enemies and destroys trees along its path. For each enemy that Powershot hits, its damage is reduced by 20%%.",
  "DOTA_Tooltip_ability_windrunner_powershot_Lore": "During her early years of training, Lyralei learned to fire powerful arrows that cleaved even trees to reach their targets.",
  "DOTA_Tooltip_ability_windrunner_powershot_Note0": "Powershot will destroy trees in its area of effect.",
  "DOTA_Tooltip_ability_windrunner_powershot_arrow_range": "RANGE:",
  "DOTA_Tooltip_ability_windrunner_powershot_powershot_damage": "DAMAGE:",
  "DOTA_Tooltip_ability_windrunner_shackleshot": "Shackleshot",
  "DOTA_Tooltip_ability_windrunner_shackleshot_Description": "Shackles the target to an enemy unit or tree in a line directly behind it.  If no unit or tree is present, the stun duration is reduced to 0.75.",
  "DOTA_Tooltip_ability_windrunner_shackleshot_Lore": "Windranger's variety of bow skills includes an arrow with thick ropes attached to encumber any escaping target.",
  "DOTA_Tooltip_ability_windrunner_shackleshot_Note0": "This spell can shackle two enemies together, stunning them both.",
  "DOTA_Tooltip_ability_windrunner_shackleshot_Note1": "Can shackle the target to an enemy unit or tree at most 525/500 behind it.",
