  "DOTA_Tooltip_ability_windrunner_windrun_Lore": "Lyralei enchants gusts of winds to fight incoming attacks.",
  "DOTA_Tooltip_ability_windrunner_windrun_Note0": "Activating Windrun will evade incoming projectiles.",
