  "DOTA_Tooltip_ability_juggernaut_blade_fury": "Blade Fury",
  "DOTA_Tooltip_ability_juggernaut_blade_fury_Description": "Causes a bladestorm of destructive force around Juggernaut, rendering him immune to magic and dealing damage to nearby enemy units. \\n\\nDISPEL TYPE: Basic Dispel",
  "DOTA_Tooltip_ability_juggernaut_blade_fury_Lore": "Yurnero's renowned katana techniques are feared by warriors and sorcerors alike.",
