  "DOTA_Tooltip_ability_phantom_lancer_doppelwalk": "Doppelganger",
  "DOTA_Tooltip_ability_phantom_lancer_doppelwalk_Description": "Phantom Lancer briefly vanishes from the battlefield. After %delay% second, Phantom Lancer and any of his nearby illusions reappear at a random position within the targeted location, along with two additional doppelgangers. Extends duration of all illusions. The two added doppelgangers have different properties: one takes normal damage and deals none, while the other takes 600%% damage and deals 20%% damage.\\n\\nDISPEL TYPE: Basic Dispel",
  "DOTA_Tooltip_ability_phantom_lancer_doppelwalk_Lore": "Dread Magus Vorn's death imbued the Phantom Lancer with the ability to bend and fracture all spectrums of light.",
