  "DOTA_Tooltip_ability_phantom_lancer_phantom_edge": "Phantom Rush",
  "DOTA_Tooltip_ability_phantom_lancer_phantom_edge_Description": "When targetting an enemy for an attack, Phantom Lancer quickly charges into range, gaining a temporary agility boost upon reaching the target. Phantom Lancer's illusions also have this ability.",
  "DOTA_Tooltip_ability_phantom_lancer_phantom_edge_Lore": "Azwraith knows that on the field of battle, speed can mean everything.",
