  "DOTA_Tooltip_ability_beastmaster_wild_axes_Description": "Beastmaster sends his axes flying and calls them home again, slicing through enemy units and trees along their path.  Each axe can hit an enemy once, and amplifies subsequent damage from Beastmaster and his units.",
  "DOTA_Tooltip_ability_beastmaster_wild_axes_Lore": "While learning to maneuver in nature alone, the Beastmaster also mastered the use of a pair of tomahawks, adept at cutting down trees as well as adversaries.",
