  "DOTA_Tooltip_ability_beastmaster_primal_roar_Description": "Beastmaster lets loose a deafening roar that stuns, and shoves open a path to its target. All units in the path of the roar are damaged, while units shoved aside by the roar have their movement and attack speed slowed. Additionally, Beastmaster and his units gain 30% movement speed for 3 seconds.\\n\\nUpgradable by Aghanim's Scepter.",
