  "DOTA_Tooltip_ability_venomancer_venomous_gale": "Venomous Gale",
  "DOTA_Tooltip_ability_venomancer_venomous_gale_Description": "Launches a ball of venom in a line, poisoning enemy units so that they take both initial damage and damage over time, as well as suffering slowed movement.  Venomous Gale deals damage every 3 seconds over its duration.",
