  "DOTA_Tooltip_ability_faceless_void_chronosphere_Description": "Creates a blister in spacetime, trapping all units caught in its sphere of influence and causes you to move very quickly inside it.  Only Faceless Void and any units he controls are unaffected.  Invisible units in the sphere will be revealed.\\n\\nUpgradable by Aghanim's Scepter.",
