  "DOTA_Tooltip_ability_pugna_life_drain": "Life Drain",
  "DOTA_Tooltip_ability_pugna_life_drain_Description": "CHANNELED - When cast on an enemy, Pugna drains health from the target enemy unit to heal himself and granting vision over the target. If Pugna has full HP, and the enemy target is a Hero, Life Drain will restore mana instead.\\n\\nWhen cast on an ally, Pugna will drain his own health into his ally.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_pugna_life_drain_Lore": "Pugna has truly become more powerful than even the grandmaster of Oblivion.",
