  "DOTA_Tooltip_ability_templar_assassin_refraction": "Refraction",
  "DOTA_Tooltip_ability_templar_assassin_refraction_Lore": "Manipulating her psionic veil, Lanaya bends nature's law to her will.",
  "DOTA_Tooltip_ability_templar_assassin_refraction_Description": "Templar Assassin becomes highly elusive, avoiding damage and gaining a bonus to her damage.  The damage and avoidance effects are separate, and have a limited number of instances.",
