  "DOTA_Tooltip_ability_templar_assassin_meld_Description": "Templar Assassin conceals herself, becoming invisible as long as she remains still.  If Meld's invisibility is broken by attacking an enemy, Lanaya will deal bonus damage to the enemy and reduce their armor for %armor_reduction_duration_tooltip% seconds.",
  "DOTA_Tooltip_ability_templar_assassin_meld_Note0": "Meld is broken by any action other than staying in position.",
