  "DOTA_Tooltip_ability_templar_assassin_psi_blades": "Psi Blades",
  "DOTA_Tooltip_ability_templar_assassin_psi_blades_Lore": "Her blades pierce the skin as much as the mind, furthering her understanding of the mystery that is reality.",
  "DOTA_Tooltip_ability_templar_assassin_psi_blades_Description": "Templar Assassin's psi blades slice through the attacked unit, splitting and damaging enemy units directly behind it, while gaining bonus attack range.",
