  "DOTA_Tooltip_ability_templar_assassin_psionic_trap_Lore": "Calling upon the reach of the Hidden Temple, none escape the eye of the Templar.",
  "DOTA_Tooltip_ability_templar_assassin_psionic_trap_Description": "Templar Assassin places mystical traps that invisibly monitor enemy movement. When sprung at her command, they exert a slowing influence of %movement_speed_min%%% in the area. Traps charge up to slow %movement_speed_max%%% after %trap_max_charge_duration% seconds. Deals bonus damage when fully charged.",
