  "DOTA_Tooltip_ability_viper_corrosive_skin_Description": "Viper exudes an infectious toxin that damages and slows the attack speed of any enemy that damages it in a %max_range_tooltip% radius. The acid exudate also increases Viper's resistance to magic.",
