  "DOTA_Tooltip_ability_luna_eclipse": "Eclipse",
  "DOTA_Tooltip_ability_luna_eclipse_Lore": "In times of great need, Selemene herself descends into the world, blocking out the light and hope of the opposed.",
  "DOTA_Tooltip_ability_luna_eclipse_Description": "Showers random nearby enemies with strikes from Luna's current level of Lucent Beam. These beams do not stun their targets, and there is a maximum number of times that a single target can be struck. Also turns day into night for 10 seconds.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_luna_eclipse_Note0": "Eclipse doesn't work if Luna doesn't have any points in Lucent Beam.",
