  "DOTA_Tooltip_ability_luna_eclipse_aghanim_description": "Allows Luna to cast Eclipse on an allied unit or herself and have its effects follow them, or cast it on an area. Also increases total beams and duration, removes the limit on beams per unit, and makes the beams appear twice as fast.",
  "DOTA_Tooltip_ability_dragon_knight_breathe_fire": "Breathe Fire",
