  "DOTA_Tooltip_ability_dragon_knight_breathe_fire_reduction": "%DAMAGE REDUCTION:",
  "DOTA_Tooltip_ability_dragon_knight_breathe_fire_duration": "REDUCTION DURATION:",
  "DOTA_Tooltip_ability_dragon_knight_breathe_fire_Lore": "Knight Davion's breath has become that of the mighty Eldwurm Slyrak, remembered for burning numerous other knights to a crisp.",
  "DOTA_Tooltip_ability_dragon_knight_breathe_fire_Note0": "Affects mechanical units.",
