  "DOTA_Tooltip_ability_dragon_knight_dragon_tail": "Dragon Tail",
  "DOTA_Tooltip_ability_dragon_knight_dragon_tail_Description": "Dragon Knight smites an enemy unit in melee range with his shield, stunning it while dealing minor damage.  When in Elder Dragon Form, the cast range increases to 400.",
  "DOTA_Tooltip_ability_dragon_knight_dragon_tail_Lore": "Davion's combination of dragon talents and knightly skills makes him a vicious opponent in melee combat.",
  "DOTA_Tooltip_ability_dragon_knight_dragon_tail_stun_duration": "STUN DURATION:",
  "DOTA_Tooltip_ability_dragon_knight_dragon_blood": "Dragon Blood",
  "DOTA_Tooltip_ability_dragon_knight_dragon_blood_Description": "The life blood of the Dragon improves health regeneration and strengthens armor.",
  "DOTA_Tooltip_ability_dragon_knight_dragon_blood_Lore": "Slyrak's blood still courses through Davion's veins, giving him twice the vitality of an ordinary knight.",
