  "DOTA_Tooltip_ability_dragon_knight_dragon_blood_bonus_armor": "ARMOR:",
  "DOTA_Tooltip_ability_dragon_knight_elder_dragon_form": "Elder Dragon Form",
  "DOTA_Tooltip_ability_dragon_knight_elder_dragon_form_Description": "Dragon Knight takes the form of one of three powerful elder dragons, increasing his speed, and Dragon Tail's range, while granting him new powers.\\n<font color=\\\"#9acd32\\\">LEVEL 1 Green Dragon</font> - Corrosive Breath: attacks deal 20 poison damage per second for 5 seconds. Works on structures.\\n<font color=\\\"#ff0000\\\">LEVEL 2 Red Dragon</font> - Splash Attack: attacks damage all enemy units in a 300 radius, with Corrosive Breath added to the targets.\\n<font color=\\\"#87ceeb\\\">LEVEL 3 Blue Dragon</font> - Frost Breath: slows movement speed by 30%% and attack speed by 30 of enemy units in Splash Attack range for 3 seconds, with Corrosive Breath added to the targets.",
  "DOTA_Tooltip_ability_dragon_knight_elder_dragon_form_Lore": "The dormant dragon power springs forth from within Davion, combining the powers of a legendary knight with a legendary Eldwurm.",
  "DOTA_Tooltip_ability_dragon_knight_elder_dragon_form_Note0": "Corrosive Breath poison damage is lethal.",
  "DOTA_Tooltip_ability_dragon_knight_elder_dragon_form_Note1": "Splash Attack deals 100/75/50% damage in a 150/225/300 radius. Splash damage does not affect buildings.",
  "DOTA_Tooltip_ability_dragon_knight_elder_dragon_form_Note2": "Manta Style's cooldown time will be based on whether Dragon Knight was in his ranged or melee form when the item was used.",
  "DOTA_Tooltip_ability_dragon_knight_elder_dragon_form_duration": "DURATION:",
