  "DOTA_Tooltip_ability_enchantress_untouchable": "Untouchable",
  "DOTA_Tooltip_ability_enchantress_untouchable_Description": "Enchantress beguiles her enemies, slowing their attacks when she is attacked.",
  "DOTA_Tooltip_ability_enchantress_untouchable_Lore": "After attacking Aiushtha, most opponents are overwhelmed with guilt having attacked such a natural beauty.",
  "DOTA_Tooltip_ability_enchantress_untouchable_Note0": "All units who directly attack Enchantress will get slowed.",
  "DOTA_Tooltip_ability_enchantress_untouchable_Note1": "The slow is applied on the attacking unit when the attack starts, not when damage is applied.",
  "DOTA_Tooltip_ability_enchantress_untouchable_slow_attack_speed": "ATTACK SLOW:",
  "DOTA_Tooltip_ability_enchantress_enchant": "Enchant",
  "DOTA_Tooltip_ability_enchantress_enchant_Description": "Enchantress charms an enemy. If the enemy is a hero, they are slowed. If the enemy is a creep, she brings it under her control. If cast on a creep already under her control, she refreshes its enchant duration.\\n\\nDISPEL TYPE: Basic Dispel",
  "DOTA_Tooltip_ability_enchantress_enchant_Lore": "A true steward of the forest, Aiushtha befriends all within it - from lowly trolls to powerful centaurs.",
  "DOTA_Tooltip_ability_enchantress_enchant_Note0": "Illusions can be converted.",
  "DOTA_Tooltip_ability_enchantress_enchant_slow_movement_speed": "%HERO MOVE SLOW:",
  "DOTA_Tooltip_ability_enchantress_enchant_tooltip_duration": "HERO SLOW DURATION:",
  "DOTA_Tooltip_ability_enchantress_enchant_dominate_duration": "CREEP ENCHANT DURATION:",
  "DOTA_Tooltip_ability_enchantress_natures_attendants": "Nature's Attendants",
  "DOTA_Tooltip_ability_enchantress_natures_attendants_Description": "A cloud of wisps heals Enchantress and any friendly units nearby. Lasts 11 seconds.",
  "DOTA_Tooltip_ability_enchantress_natures_attendants_Lore": "Whimsical spirits of the woods possess a healing power that is quite subtle, but gains strength in numbers.",
  "DOTA_Tooltip_ability_enchantress_natures_attendants_Note0": "Units with full HP will not be selected for the heal.",
  "DOTA_Tooltip_ability_enchantress_natures_attendants_Note1": "Can heal units with spell immunity.",
  "DOTA_Tooltip_ability_enchantress_natures_attendants_wisp_count": "WISPS:",
  "DOTA_Tooltip_ability_enchantress_natures_attendants_heal": "HEAL PER SECOND:",
  "DOTA_Tooltip_ability_enchantress_natures_attendants_radius": "RADIUS:",
  "DOTA_Tooltip_ability_enchantress_impetus": "Impetus",
  "DOTA_Tooltip_ability_enchantress_impetus_Description": "Places an enchantment on each attack while activated, causing it to deal additional damage based on how far away the target is.  The farther the target, the greater the damage dealt.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_enchantress_impetus_Lore": "While Aiushtha favors peaceful means, she is capable of a magical onslaught from a distance, her spears gaining energy from the natural life around her.",
  "DOTA_Tooltip_ability_enchantress_impetus_Note0": "The damage is capped at 1750 distance.",
  "DOTA_Tooltip_ability_enchantress_impetus_Note1": "Distance to target is measured when the projectile hits.",
  "DOTA_Tooltip_ability_enchantress_impetus_distance_damage_pct": "%DISTANCE DAMAGE:",
  "DOTA_Tooltip_ability_enchantress_impetus_bonus_attack_range_scepter": "SCEPTER BONUS RANGE:",
  "DOTA_Tooltip_ability_enchantress_impetus_aghanim_description": "Increases attack range, and cast range of Impetus.",
  "DOTA_Tooltip_ability_omniknight_purification": "Purification",
  "DOTA_Tooltip_ability_omniknight_purification_Description": "Instantly heals a friendly unit and damages all nearby enemy units.",
  "DOTA_Tooltip_ability_omniknight_purification_Lore": "Priests of the Omniscience are adept at removing impurities from fellow warriors.",
  "DOTA_Tooltip_ability_omniknight_purification_heal": "HEAL/DAMAGE:",
  "DOTA_Tooltip_ability_omniknight_purification_radius": "DAMAGE RADIUS:",
  "DOTA_Tooltip_ability_omniknight_repel": "Repel",
  "DOTA_Tooltip_ability_omniknight_repel_Description": "Creates a divine barrier around an ally which grants them Spell Immunity and 100%% resistance to Magic Damage.\\n\\nDISPEL TYPE: Basic Dispel",
  "DOTA_Tooltip_ability_omniknight_repel_Note0": "Repel acts the same as other sources of spell immunity, and cannot be purged.",
  "DOTA_Tooltip_ability_omniknight_repel_Lore": "When faced with the pit of sacrifice, Purist's resilience to magic was tested and affirmed.",
  "DOTA_Tooltip_ability_omniknight_repel_duration": "DURATION:",
  "DOTA_Tooltip_ability_omniknight_degen_aura": "Degen Aura",
  "DOTA_Tooltip_ability_omniknight_degen_aura_Description": "Greatly degenerates the movement and attack capabilities of enemy units that stray too near.",
  "DOTA_Tooltip_ability_omniknight_degen_aura_Note0": "Does not affect Spell Immune units.",
  "DOTA_Tooltip_ability_omniknight_degen_aura_Lore": "The holy embodiment of Purist Thunderwrath is enough to weaken those of lesser faith.",
