  "DOTA_Tooltip_ability_omniknight_guardian_angel": "Guardian Angel",
  "DOTA_Tooltip_ability_omniknight_guardian_angel_Description": "Omniknight summons a Guardian Angel that grants immunity from physical damage.\\n\\nUpgradable by Aghanim's Scepter",
  "DOTA_Tooltip_ability_omniknight_guardian_angel_Lore": "The All Seeing One casts down his protection over those that Purist calls friend.",
  "DOTA_Tooltip_ability_omniknight_guardian_angel_Note0": "Affects allies with spell immunity.",
  "DOTA_Tooltip_ability_omniknight_guardian_angel_Note1": "Can be purged.",
