  "DOTA_Tooltip_ability_huskar_inner_vitality": "Inner Vitality",
  "DOTA_Tooltip_ability_huskar_inner_vitality_Description": "Unlocks the regenerative power of a friendly unit, with healing based upon its primary attribute. If the target is below 35%%, it will heal faster.  Lasts 16 seconds.",
  "DOTA_Tooltip_ability_huskar_inner_vitality_Lore": "While Huskar has little use for magic, this minor enchantment was learned from the Dazzle and the Dezun priests, to be used in times of great injury.",
  "DOTA_Tooltip_ability_huskar_inner_vitality_Note0": "The HP percentage is checked every second and the regeneration adjusted accordingly.",
  "DOTA_Tooltip_ability_huskar_inner_vitality_Note1": "Can be cast on Spell Immune units.",
  "DOTA_Tooltip_ability_huskar_inner_vitality_heal": "HEALTH REGEN:",
