  "DOTA_Tooltip_ability_huskar_inner_vitality_tooltip_hurt_attrib_bonus": "%BONUS WHEN HURT:",
  "DOTA_Tooltip_ability_huskar_burning_spear": "Burning Spear",
  "DOTA_Tooltip_ability_huskar_burning_spear_Description": "Huskar sets his spears aflame, dealing damage over time with his regular attack.  Multiple attacks will stack additional damage.  Each attack drains some of Huskar's health.  Lasts 8 seconds.",
  "DOTA_Tooltip_ability_huskar_burning_spear_Lore": "The Sacred Warrior ignites his weaponry after marking it with his own blood - causing far greater pain than he himself feels.",
  "DOTA_Tooltip_ability_huskar_burning_spear_Note0": "Burning Spear stacks additively when used multiple times on one target.  There is no cap.",
  "DOTA_Tooltip_ability_huskar_burning_spear_Note1": "Huskar cannot kill himself with this skill.",
  "DOTA_Tooltip_ability_huskar_burning_spear_health_cost": "HEALTH COST:",
  "DOTA_Tooltip_ability_huskar_burning_spear_burn_damage": "BURN DAMAGE:",
  "DOTA_Tooltip_ability_huskar_berserkers_blood": "Berserker's Blood",
  "DOTA_Tooltip_ability_huskar_berserkers_blood_Description": "Huskar's injuries feed his power, giving increased attack speed and magic damage resistance based on missing health.",
  "DOTA_Tooltip_ability_huskar_berserkers_blood_Lore": "After losing his birthright, Huskar gained a lethality in physical combat that is only heightened as his own blood is spilled.",
  "DOTA_Tooltip_ability_huskar_berserkers_blood_Note0": "Maximum bonus is achieved when Huskar has strictly less than 10% of his maximum HP.",
  "DOTA_Tooltip_ability_huskar_berserkers_blood_Note1": "Bonuses are provided linearly, no bonus at 100% HP and full bonus at 10% HP.",
  "DOTA_Tooltip_ability_huskar_berserkers_blood_maximum_attack_speed": "MAX ATTACK SPEED:",
  "DOTA_Tooltip_ability_huskar_berserkers_blood_maximum_resistance": "%MAX MAGIC RESISTANCE:",
  "DOTA_Tooltip_ability_huskar_berserkers_blood_hp_threshold_max": "%HP FOR MAX BONUS:",
  "DOTA_Tooltip_ability_huskar_life_break": "Life Break",
  "DOTA_Tooltip_ability_huskar_life_break_Description": "Huskar draws upon his health to break an enemy's life, leaping at a target within attack range to shatter a percentage of that hero's current health, and slowing them.  While leaping, Huskar is Spell Immune.\\n\\nDISPEL TYPE: Basic Dispel\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_huskar_life_break_Lore": "No matter the danger, Huskar thrusts himself into melees that only he can survive.",
  "DOTA_Tooltip_ability_huskar_life_break_Note0": "If Huskar is disabled during the charge or the target moves more than 1400 units in 0.015 seconds, the charge stops.",
  "DOTA_Tooltip_ability_huskar_life_break_Note1": "Slow works on Spell Immune units.",
  "DOTA_Tooltip_ability_huskar_life_break_Note2": "Life Break cannot be disjointed.",
  "DOTA_Tooltip_ability_huskar_life_break_tooltip_health_damage": "%DAMAGE DEALT:",
  "DOTA_Tooltip_ability_huskar_life_break_tooltip_health_cost_percent": "%DAMAGE TAKEN:",
