  "DOTA_Tooltip_ability_night_stalker_void": "Void",
  "DOTA_Tooltip_ability_night_stalker_void_Description": "Creates a damaging void that slows an enemy unit and deals damage.  Void also mini-stuns, interrupting channeling abilities. The slowing effect lasts longer at night. Deals half damage during day.",
  "DOTA_Tooltip_ability_night_stalker_void_Lore": "Balanar creates a vortex of infinite night, tearing opponents violently into the eternal darkness that once was.",
