  "DOTA_Tooltip_ability_night_stalker_crippling_fear_Description": "Night Stalker horrifies the target enemy Hero, causing it to miss attacks and become silenced.  The effect lasts longer at night.",
  "DOTA_Tooltip_ability_night_stalker_crippling_fear_Lore": "A vision of the twisted maw of Balanar etches itself into the minds of the poor souls unlucky enough to cross his path.",
