  "DOTA_Tooltip_ability_night_stalker_hunter_in_the_night": "Hunter in the Night",
  "DOTA_Tooltip_ability_night_stalker_hunter_in_the_night_Description": "Night Stalker is in his element at night, attacking and moving with great swiftness. Can be activated during night to grant Night Stalker flying movement for %duration% seconds.",
  "DOTA_Tooltip_ability_night_stalker_hunter_in_the_night_Lore": "The hunting prowess of Balanar improves as the night beckons.",
  "DOTA_Tooltip_ability_night_stalker_hunter_in_the_night_bonus_movement_speed_pct_night": "%MOVE SPEED:",
  "DOTA_Tooltip_ability_night_stalker_hunter_in_the_night_bonus_attack_speed_night": "ATTACK SPEED:",
  "DOTA_Tooltip_ability_night_stalker_darkness": "Darkness",
  "DOTA_Tooltip_ability_night_stalker_darkness_Description": "Night Stalker smothers the sun and summons instant darkness, so that he might use his powers at their fullest. While Darkness is in effect, enemy heroes, units and wards have their vision range reduced. \\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_night_stalker_darkness_Lore": "It is a humbling sight to see when the mightiest of warriors become afraid of the dark.",
  "DOTA_Tooltip_ability_night_stalker_darkness_duration": "DURATION:",
  "DOTA_Tooltip_ability_night_stalker_darkness_blind_percentage": "VISION REDUCTION:",
  "DOTA_Tooltip_ability_night_stalker_darkness_aghanim_description": "Grants unobstructed vision in the night. The improved vision does not depend on using Darkness.",
  "DOTA_Tooltip_ability_broodmother_spawn_spiderlings": "Spawn Spiderlings",
  "DOTA_Tooltip_ability_broodmother_spawn_spiderlings_Description": "Broodmother injects her young into an enemy unit, dealing damage. The spiderlings will hatch if the target is killed while under this influence.",
  "DOTA_Tooltip_ability_broodmother_spawn_spiderlings_Lore": "Black Arachnia continues to raise her young, even amidst the field of combat.  The brood quickly learns how to support their mother.",
  "DOTA_Tooltip_ability_broodmother_spawn_spiderlings_Note0": "If the target is killed within 2 seconds of being hit with this skill, the Spiderlings will spawn.",
  "DOTA_Tooltip_ability_broodmother_spawn_spiderlings_Note1": "Units killed by Spiderlings will then give birth to Spiderites, smaller and less powerful versions of the Spiderlings.",
  "DOTA_Tooltip_ability_broodmother_spawn_spiderlings_spiderling_duration": "LIFETIME:",
  "DOTA_Tooltip_ability_broodmother_spawn_spiderlings_damage": "DAMAGE:",
  "DOTA_Tooltip_ability_broodmother_spawn_spiderlings_count": "COUNT:",
  "DOTA_Tooltip_ability_broodmother_spin_web": "Spin Web",
  "DOTA_Tooltip_ability_broodmother_spin_web_Description": "Spins a large web that grants Broodmother a passive movement speed increase, gives free movement, and boosts regeneration while in its vicinity. Spin Web charges restore every %charge_restore_time% seconds. Spin Web can be cast from anywhere as long as the new web touches an existing web. Webs never expire, and can be manually destroyed. When the maximum limit of webs is exceeded, the oldest web disappears.",
  "DOTA_Tooltip_ability_broodmother_spin_web_Lore": "Weaving a bed of silken fibers, Arachnia's web both protects her and her offspring, as well as giving advancing opponents a sense of forboding.",
  "DOTA_Tooltip_ability_broodmother_spin_web_Note0": "Also affects Spiderling and Spiderites.",
  "DOTA_Tooltip_ability_broodmother_spin_web_Note1": "If free movement is disabled, you will destroy nearby trees.",
  "DOTA_Tooltip_ability_broodmother_spin_web_count": "MAX WEBS:",
  "DOTA_Tooltip_ability_broodmother_spin_web_heath_regen": "HEALTH REGEN:",
  "DOTA_Tooltip_ability_broodmother_spin_web_bonus_movespeed": "%MOVE INCREASE:",
  "DOTA_Tooltip_ability_broodmother_spin_web_max_charges": "MAX WEB CHARGES:",
  "DOTA_Tooltip_ability_broodmother_spin_web_charge_restore_time": "CHARGE RESTORE TIME:",
  "DOTA_Tooltip_ability_broodmother_spin_web_destroy": "Destroy Spin Web",
  "DOTA_Tooltip_ability_broodmother_spin_web_destroy_Description": "Destroys the selected Spin Web.",
  "DOTA_Tooltip_ability_broodmother_incapacitating_bite": "Incapacitating Bite",
  "DOTA_Tooltip_ability_broodmother_incapacitating_bite_Description": "Broodmother's venom cripples enemy units, causing her attacks to slow and giving the affected unit a chance to miss its attacks.",
  "DOTA_Tooltip_ability_broodmother_incapacitating_bite_Lore": "Paralytic toxins come from Black Arachnia's fangs, causing a slow and painful death to those who intrude on her webs.",
  "DOTA_Tooltip_ability_broodmother_incapacitating_bite_Note0": "The miss chance stacks with evasion and terrain dodge chance.",
  "DOTA_Tooltip_ability_broodmother_incapacitating_bite_miss_chance": "%MISS CHANCE:",
  "DOTA_Tooltip_ability_broodmother_incapacitating_bite_bonus_movespeed": "%MOVE SLOW:",
  "DOTA_Tooltip_ability_broodmother_incapacitating_bite_duration": "DURATION:",
  "DOTA_Tooltip_ability_broodmother_insatiable_hunger": "Insatiable Hunger",
  "DOTA_Tooltip_ability_broodmother_insatiable_hunger_Description": "A violent lust for vital fluids increases Broodmother's attack damage and gives her a vampiric attack.",
  "DOTA_Tooltip_ability_broodmother_insatiable_hunger_Lore": "While most of her prey is wrapped in silken cocoons and saved for her young, the Broodmother herself has a taste for wandering heroes.",
  "DOTA_Tooltip_ability_broodmother_insatiable_hunger_Note0": "Fully stacks with other sources of lifesteal.",
