  "DOTA_Tooltip_ability_broodmother_spawn_spiderite": "Spawn Spiderite",
  "DOTA_Tooltip_ability_broodmother_spawn_spiderite_Description": "Applies debuff on attack. If debuffed unit dies, a spiderite will spawn.",
  "DOTA_Tooltip_ability_broodmother_spawn_spiderite_spiderite_duration": "LIFETIME:",
