  "DOTA_Tooltip_ability_bounty_hunter_shuriken_toss": "Shuriken Toss",
  "DOTA_Tooltip_ability_bounty_hunter_shuriken_toss_Description": "Hurls a deadly shuriken at an enemy unit, dealing damage and mini-stunning the target.  The shuriken will bounce between any Tracked units who are within a %bounce_aoe% radius of each other.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_bounty_hunter_shuriken_toss_aghanim_description": "Allows Shuriken Toss to bounce twice on each hero.",
  "DOTA_Tooltip_ability_bounty_hunter_shuriken_toss_Lore": "While the shuriken may be small, Gondar's precise aim can cause critical damage.",
  "DOTA_Tooltip_ability_bounty_hunter_shuriken_toss_Note0": "This spell interrupts channeling abilities.",
  "DOTA_Tooltip_ability_bounty_hunter_shuriken_toss_bonus_damage": "DAMAGE:",
