  "DOTA_Tooltip_ability_bounty_hunter_jinada": "Jinada",
  "DOTA_Tooltip_ability_bounty_hunter_jinada_Description": "Bounty Hunter plans his next hit, passively adding a critical strike and maim to his next attack.",
  "DOTA_Tooltip_ability_bounty_hunter_jinada_Lore": "Whispering an enchantment he learned from Soruq to his faithful blades, Gondar targets vital tendons and joints to disable his opponents.",
  "DOTA_Tooltip_ability_bounty_hunter_jinada_crit_multiplier": "%CRITICAL DAMAGE:",
