  "DOTA_Tooltip_ability_bounty_hunter_wind_walk": "Shadow Walk",
  "DOTA_Tooltip_ability_bounty_hunter_wind_walk_Description": "Bounty Hunter becomes invisible and gains the ability to move through other units until he attacks or uses an ability.  If he breaks the invisibility with an attack, that attack will deal bonus damage.",
  "DOTA_Tooltip_ability_bounty_hunter_wind_walk_Lore": "The court jesters present during King Goff's assassination can recount no other image than a dancing shadow.",
  "DOTA_Tooltip_ability_bounty_hunter_wind_walk_Note0": "Will not break most channeling of spells upon activation.",
  "DOTA_Tooltip_ability_bounty_hunter_wind_walk_Note1": "The bonus damage is a separate instance, and isn't considered in Jinada's calculation.",
  "DOTA_Tooltip_ability_bounty_hunter_wind_walk_duration": "DURATION:",
  "DOTA_Tooltip_ability_bounty_hunter_wind_walk_fade_time": "FADE TIME:",
  "DOTA_Tooltip_ability_bounty_hunter_wind_walk_bonus_damage": "BONUS DAMAGE:",
  "DOTA_Tooltip_ability_bounty_hunter_track": "Track",
  "DOTA_Tooltip_ability_bounty_hunter_track_Description": "Tracks an enemy hero, granting True Sight of the target, and information on how much gold it is carrying. Allies near the hunted are granted bonus movement speed. If the target dies, Bounty Hunter and nearby heroes collect a bonus bounty of gold.",
  "DOTA_Tooltip_ability_bounty_hunter_track_Lore": "Using his elevated senses of sight and smell, Gondar's hits have quite a good chance of success.",
  "DOTA_Tooltip_ability_bounty_hunter_track_Note0": "Bounty Hunter will get the bonus gold if the target dies while Track is active, regardless of how it died.",
  "DOTA_Tooltip_ability_bounty_hunter_track_Note1": "Track's effect is only visible to allies.",
  "DOTA_Tooltip_ability_bounty_hunter_track_Note2": "The allies that are receiving the bonus speed from Track are the ones that will receive the bonus gold.",
  "DOTA_Tooltip_ability_bounty_hunter_track_Note3": "Bonus gold is reliable gold.",
