  "DOTA_Tooltip_ability_bounty_hunter_track_bonus_gold_self": "BONUS GOLD FOR SELF:",
  "DOTA_Tooltip_ability_bounty_hunter_track_duration": "DURATION:",
  "DOTA_Tooltip_ability_bounty_hunter_track_cast_range_tooltip": "CAST RANGE:",
  "DOTA_Tooltip_ability_weaver_the_swarm": "The Swarm",
  "DOTA_Tooltip_ability_weaver_the_swarm_Description": "Weaver launches a swarm of 12 young Weavers that latch on any enemy unit in their path, attacking and reducing armor until it is killed.",
  "DOTA_Tooltip_ability_weaver_the_swarm_Lore": "Skitskurr opens a gap in the space time fabric, allowing young Weavers to slip through and aid him in combat.",
  "DOTA_Tooltip_ability_weaver_the_swarm_Note0": "When a beetle latches on a target, it will remain there until it is killed or the duration expires.",
  "DOTA_Tooltip_ability_weaver_the_swarm_Note1": "Beetles are Spell Immune, but can be killed in 4 attacks from a hero, or 8 from other units.",
  "DOTA_Tooltip_ability_weaver_the_swarm_Note2": "Beetles provide 321/321 sight unless the target is invisible.",
