  "DOTA_Tooltip_ability_weaver_shukuchi": "Shukuchi",
  "DOTA_Tooltip_ability_weaver_shukuchi_Description": "Weaver shifts out of visibility, gaining the ability to move at maximum speed through physical units--doing harm to any enemies it passes through.",
  "DOTA_Tooltip_ability_weaver_shukuchi_Lore": "As the Weavers worked in the fabric of creation, small wormholes allowed them to slip through time to better work their craft.",
  "DOTA_Tooltip_ability_weaver_shukuchi_Note0": "Can only damage the same unit once per cast.",
