  "DOTA_Tooltip_ability_weaver_geminate_attack": "Geminate Attack",
  "DOTA_Tooltip_ability_weaver_geminate_attack_Description": "Allows Weaver to dispatch two attacks at once.",
  "DOTA_Tooltip_ability_weaver_geminate_attack_Lore": "Skitskurr's relationship with time is somewhat variable, causing his actions to be witnessed - and felt - more than once.",
  "DOTA_Tooltip_ability_weaver_geminate_attack_Note0": "The second attack leaves Weaver 0.25 seconds after the first, and has no range limit.",
  "DOTA_Tooltip_ability_weaver_geminate_attack_tooltip_attack": "GEMINATE ATTACK COUNT:",
  "DOTA_Tooltip_ability_weaver_time_lapse": "Time Lapse",
  "DOTA_Tooltip_ability_weaver_time_lapse_Description": "Weaver warps backward to whatever position it was in five seconds earlier--regaining the HP and mana from that time. No effect on cooldown, gold or experience.\\n\\nUpgradable by Aghanim's Scepter.\\n\\nDISPEL TYPE: Strong Dispel",
  "DOTA_Tooltip_ability_weaver_time_lapse_cooldown_scepter": "SCEPTER COOLDOWN:",
  "DOTA_Tooltip_ability_weaver_time_lapse_cast_range_tooltip_scepter": "SCEPTER ALLY CAST RANGE:",
  "DOTA_Tooltip_ability_weaver_time_lapse_Lore": "If Skitskurr does not deem the current reality of the world to fit his desires, he simply crawls back in time to right what was wronged.",
  "DOTA_Tooltip_ability_weaver_time_lapse_Note0": "Removes most negative buffs from Weaver.",
  "DOTA_Tooltip_ability_weaver_time_lapse_Note1": "Time Lapse disjoints projectiles.",
  "DOTA_Tooltip_ability_weaver_time_lapse_aghanim_description": "Reduces cooldown, and allows Weaver to cast Time Lapse on an ally.",
  "DOTA_Tooltip_ability_jakiro_dual_breath": "Dual Breath",
  "DOTA_Tooltip_ability_jakiro_dual_breath_Description": "An icy blast followed by a wave of fire launches out in a path in front of Jakiro. The ice slows enemies, while the fire delivers damage over time.",
  "DOTA_Tooltip_ability_jakiro_dual_breath_Lore": "Pyrexae dragons have tremendously dangerous breath for each branch of the species; combining them is unimaginable.",
  "DOTA_Tooltip_ability_jakiro_dual_breath_Note0": "Can hit units up to 1025 range away.",
  "DOTA_Tooltip_ability_jakiro_dual_breath_Note1": "Has a 0.3 second interval between each breath.",
