  "DOTA_Tooltip_ability_jakiro_ice_path": "Ice Path",
  "DOTA_Tooltip_ability_jakiro_ice_path_Description": "Creates a path of ice that stuns and damages enemies that touch it.",
  "DOTA_Tooltip_ability_jakiro_ice_path_Lore": "Jakiro's cunning right head calls upon its frozen heritage, glazing the earth with blistering cold.",
  "DOTA_Tooltip_ability_jakiro_ice_path_Note0": "It takes 0.5 seconds for the path to reach its full extent.",
