  "DOTA_Tooltip_ability_jakiro_liquid_fire": "Liquid Fire",
  "DOTA_Tooltip_ability_jakiro_liquid_fire_Description": "Jakiro burns his enemies in an area of effect with fire added to his attack, while slowing their attacks.",
  "DOTA_Tooltip_ability_jakiro_liquid_fire_Lore": "The left maw of Jakiro opens, laying waste to any in its path.",
  "DOTA_Tooltip_ability_jakiro_liquid_fire_Note0": "Works on both units and buildings.",
