  "DOTA_Tooltip_ability_jakiro_macropyre": "Macropyre",
  "DOTA_Tooltip_ability_jakiro_macropyre_Description": "Jakiro exhales a wide line of lasting flames, which deals damage per second to any enemy units caught in the fire.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_jakiro_macropyre_Lore": "Ice and fire combine to rip the battlefield to shreds with extreme temperatures.",
  "DOTA_Tooltip_ability_jakiro_macropyre_Note0": "Macropyre destroys trees in its path.",
