  "DOTA_Tooltip_ability_jakiro_macropyre_aghanim_description": "Increases flame length, duration, and damage.",
  "DOTA_Tooltip_ability_batrider_sticky_napalm": "Sticky Napalm",
  "DOTA_Tooltip_ability_batrider_sticky_napalm_Description": "Drenches an area in sticky oil, amplifying damage from Batrider's attacks and abilities and slowing the movement speed and turn rate of enemies in the area.  Additional casts of Sticky Napalm continue to increase damage, up to 10 stacks.  The extra damage is halved against creeps.",
  "DOTA_Tooltip_ability_batrider_sticky_napalm_Lore": "It's not uncommon to hear the Rider cackle while he increases the flammability of his opponents.",
  "DOTA_Tooltip_ability_batrider_sticky_napalm_Note0": "All damage from Batrider gets amplified, except for Radiance, Orb of Venom, and Urn of Shadows.",
  "DOTA_Tooltip_ability_batrider_sticky_napalm_damage": "EXTRA DAMAGE:",
  "DOTA_Tooltip_ability_batrider_sticky_napalm_duration": "DURATION:",
