  "DOTA_Tooltip_ability_ancient_apparition_ice_blast": "Ice Blast",
  "DOTA_Tooltip_ability_ancient_apparition_ice_blast_Description": "Launches a tracer toward any location on the battlefield, which must be triggered again to mark the area to be blasted by a damaging explosion of hail. The further the tracer travels, the larger the explosion will be. Enemies caught in the explosion, or who touch the icy ball of hail as it travels, are Frostbitten, taking damage and prevented from regenerating or healing. If a Frostbitten unit's health drops below a certain percentage, they will instantly shatter.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_ancient_apparition_ice_blast_Lore": "Ice storms from ages past flow through Kaldr's frosty limbs, crashing into the world and turning its inhabitants into monuments to his eternal power.",
  "DOTA_Tooltip_ability_ancient_apparition_ice_blast_Note0": "The area of effect is 275 + 50*TimeTraveled, capped at 1000.  The freeze area along the path is 275.",
  "DOTA_Tooltip_ability_ancient_apparition_ice_blast_Note1": "Kill will be granted to the source of the damage that triggers the shatter.",
  "DOTA_Tooltip_ability_ancient_apparition_ice_blast_Note2": "The instant kill doesn't work on illusions.",
  "DOTA_Tooltip_ability_ancient_apparition_ice_blast_Note3": "The HP Freeze prevents most kinds of healing, including the fountain.",
  "DOTA_Tooltip_ability_ancient_apparition_ice_blast_Note4": "The Frostbitten debuff cannot be purged.",
