  "DOTA_Tooltip_ability_alchemist_unstable_concoction_Lore": "A silver lining to the failure of turning a mountain into gold, this volatile solution has destructive potential.",
  "DOTA_Tooltip_ability_alchemist_unstable_concoction": "Unstable Concoction",
  "DOTA_Tooltip_ability_alchemist_unstable_concoction_Description": "Alchemist brews up an unstable concoction that he can throw at an enemy hero, to stun and deal damage in an area around the explosion. The longer the concoction brews, the more damage it deals and the longer the stun. After 5 seconds, the brew reaches its maximum damage and stun time. However, after 5.5 seconds, the concoction will explode on Alchemist himself if not thrown.",
