  "DOTA_Tooltip_ability_alchemist_unstable_concoction_Note1": "A timer above Alchemist's head will indicate the remaining brew time. This timer is visible to enemies.",
  "DOTA_Tooltip_ability_alchemist_unstable_concoction_Note2": "You can deny yourself with this skill.",
  "DOTA_Tooltip_ability_alchemist_unstable_concoction_Note3": "Unstable Concoction cannot be disjointed.",
  "DOTA_Tooltip_ability_alchemist_unstable_concoction_Note4": "Once thrown, Unstable Concoction stops brewing.",
