  "DOTA_Tooltip_ability_lone_druid_spirit_bear": "Summon Spirit Bear",
  "DOTA_Tooltip_ability_lone_druid_spirit_bear_Lore": "Sylla's lifelong companion is symbiotic with his spirit and heart, coming to aid him in any time of need.",
  "DOTA_Tooltip_ability_lone_druid_spirit_bear_Description": "Summons a powerful Spirit Bear companion that can equip items.  If the bear moves 1100 distance away from the Lone Druid, it cannot attack.  Lone Druid suffers %backlash_damage%%% of his max health as backlash damage if the Spirit Bear dies.  As the bear increases in levels, it can learn the Return, Entangling Claws, and Demolish abilities.  Spirit Bear does not benefit from attributes.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_lone_druid_spirit_bear_aghanim_description": "Allows Spirit Bear to attack at any range from Lone Druid, and to survive if Lone Druid dies.",
  "DOTA_Tooltip_ability_lone_druid_spirit_bear_Note0": "Most spells interact with the Spirit Bear in the same way as they do with heroes.",
  "DOTA_Tooltip_ability_lone_druid_spirit_bear_Note1": "You can't resummon the bear if it has taken damage in the past 3 seconds.",
  "DOTA_Tooltip_ability_lone_druid_spirit_bear_Note2": "Using Hand of Midas from Spirit Bear grants the earned XP to Lone Druid.",
