  "DOTA_Tooltip_ability_lone_druid_spirit_bear_entangle": "Entangling Claws",
  "DOTA_Tooltip_ability_lone_druid_spirit_bear_entangle_Description": "Attacks have a chance to cause roots to burst from the ground, immobilizing the attacked enemy unit, and dealing damage per second.",
  "DOTA_Tooltip_ability_lone_druid_spirit_bear_entangle_Note0": "Entangled units can't use Blink abilities, and are revealed if they are invisible.",
