  "DOTA_Tooltip_ability_lone_druid_savage_roar_Lore": "Sylla studies and masters the arts of the lost Bear Clan, enhancing his attunement with the wild.",
  "DOTA_Tooltip_ability_lone_druid_savage_roar_Description": "Lone Druid roars fiercely causing nearby enemies to flee towards their base in terror. Their movement speed is increased by %bonus_speed%%%.",
