  "DOTA_Tooltip_ability_lone_druid_true_form_Lore": "The mighty bear is the king of the forest, possessing the throne through raw power.",
  "DOTA_Tooltip_ability_lone_druid_true_form_Description": "Lone Druid learns to morph himself into a raging bear, losing his ranged advantage and some base movement speed, but gaining melee power as well as the Battle Cry ability.  He can morph freely between druid and bear form.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_lone_druid_true_form_Note0": "You can dodge stuns during the 2 second duration of transforming.",
