  "DOTA_Tooltip_ability_brewmaster_primal_split": "Primal Split",
  "DOTA_Tooltip_ability_brewmaster_primal_split_Lore": "It isn't clear whether Mangix is consciously aware of his potent bond with nature, as it often occurs in the midst of a drunken stupor.",
  "DOTA_Tooltip_ability_brewmaster_primal_split_Description": "Splits Brewmaster into elements, forming 3 specialized warriors, adept at survival. If any of them survive until the end of their summoned timer, the Brewmaster is reborn.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_brewmaster_primal_split_Note0": "The Earth Element will carry all auras that the Brewmaster has, as well as True Sight.",
  "DOTA_Tooltip_ability_brewmaster_primal_split_Note1": "The Fire Element has phased movement.",
  "DOTA_Tooltip_ability_brewmaster_primal_split_Note2": "When the spell ends, Brewmaster will take the place of an element in this order, assuming they are alive: Earth, Storm, Fire.",
  "DOTA_Tooltip_ability_brewmaster_primal_split_duration": "DURATION:",
  "DOTA_Tooltip_ability_brewmaster_primal_split_aghanim_description": "Earth gains Thunder Clap, Storm gains Drunken Haze and Fire gains Drunken Brawler.",
