  "DOTA_Tooltip_ability_shadow_demon_soul_catcher_Description": "Captures the soul of a random enemy in an area, amplifies all damage they take. Units under the effect of Disruption can still be affected by Soul Catcher.",
  "DOTA_Tooltip_ability_shadow_demon_soul_catcher_Lore": "Trying to resist the Shadow Demon's corruption is mostly a useless endeavor.",
