  "DOTA_Tooltip_ability_shadow_demon_shadow_poison": "Shadow Poison",
  "DOTA_Tooltip_ability_shadow_demon_shadow_poison_Description": "Deals damage in a line, and afflicts enemy units with a poison effect. The poison deals 1/2/4/8/16 times the stack damage based on the number of stacks on the target, up to 5 stacks. Additional stacks cause 50 damage each.  This deferred damage is dealt when Shadow Poison's duration is expired, or the Release sub-ability is used.",
  "DOTA_Tooltip_ability_shadow_demon_shadow_poison_Lore": "The ever growing influence of the Shadow Demon can pollute the most valiant and pure of heroes.",
  "DOTA_Tooltip_ability_shadow_demon_shadow_poison_Note0": "Shadow Poison can affect units under Disruption.",
  "DOTA_Tooltip_ability_shadow_demon_shadow_poison_stack_damage": "STACK DAMAGE:",
