  "DOTA_Tooltip_ability_shadow_demon_shadow_poison_max_multiply_stacks": "MAX STACKS TO MULTIPLY:",
  "DOTA_Tooltip_ability_shadow_demon_shadow_poison_release": "Shadow Poison Release",
  "DOTA_Tooltip_ability_shadow_demon_shadow_poison_release_Description": "Releases the poison to do damage on all affected enemies. Units under the effect of Disruption can still be affected by Shadow Poison.",
  "DOTA_Tooltip_ability_shadow_demon_demonic_purge": "Demonic Purge",
  "DOTA_Tooltip_ability_shadow_demon_demonic_purge_Description": "Purges the target enemy unit, removing positive buffs, and slowing the target for the duration.  The unit slowly regains its speed until the end of the duration, upon which damage is dealt.  Units under the effect of Disruption can still be affected by Demonic Purge.\\n\\nUpgradable by Aghanim's Scepter.\\n\\nDISPEL TYPE: Basic Dispel",
  "DOTA_Tooltip_ability_shadow_demon_demonic_purge_Lore": "Once Shadow Demon no longer has any need for his collected cultist, he releases it from its subservience - and its life.",
  "DOTA_Tooltip_ability_shadow_demon_demonic_purge_Note0": "Demonic Purge will slow Spell Immune units.",
  "DOTA_Tooltip_ability_shadow_demon_demonic_purge_tooltip_duration": "DURATION:",
  "DOTA_Tooltip_ability_shadow_demon_demonic_purge_purge_damage": "PURGE DAMAGE:",
  "DOTA_Tooltip_ability_shadow_demon_demonic_purge_charge_restore_time_tooltip_scepter": "SCEPTER CHARGE RESTORE TIME:",
  "DOTA_Tooltip_ability_shadow_demon_demonic_purge_aghanim_description": "Cooldown is removed, replaced with 3 charges that replenish every 40 seconds. Also causes Demonic Purge to break its target's passive abilities while active.",
  "DOTA_Tooltip_ability_chaos_knight_chaos_bolt": "Chaos Bolt",
  "DOTA_Tooltip_ability_chaos_knight_chaos_bolt_Description": "Throws a mysterious bolt of energy at the target unit. It stuns for a random duration and deals random damage.",
  "DOTA_Tooltip_ability_chaos_knight_chaos_bolt_Lore": "Even Chaos Knight cannot predict this manifest of unholy energy.",
  "DOTA_Tooltip_ability_chaos_knight_chaos_bolt_stun_min": "MINIMUM STUN:",
  "DOTA_Tooltip_ability_chaos_knight_chaos_bolt_stun_max": "MAXIMUM STUN:",
  "DOTA_Tooltip_ability_chaos_knight_chaos_bolt_damage_min": "MINIMUM DAMAGE:",
  "DOTA_Tooltip_ability_chaos_knight_chaos_bolt_damage_max": "MAXIMUM DAMAGE:",
  "DOTA_Tooltip_ability_chaos_knight_chaos_bolt_Note0": "The stun duration and damage values dealt are inversely related.",
  "DOTA_Tooltip_ability_chaos_knight_reality_rift": "Reality Rift",
  "DOTA_Tooltip_ability_chaos_knight_reality_rift_Description": "Teleports you, any images you have and the target unit to a random point along the line between the two of you. Reduces the target's armor for %armor_duration% seconds.",
  "DOTA_Tooltip_ability_chaos_knight_reality_rift_Lore": "Armageddon rides between the planes, bringing Chaos Knight to his victim wherever he may hide.",
  "DOTA_Tooltip_ability_chaos_knight_reality_rift_Note0": "Chaos Knight and the target unit are made to face each other, and Chaos Knight and his phantasms are issued an attack order against the target.",
  "DOTA_Tooltip_ability_chaos_knight_reality_rift_cast_range": "RANGE:",
  "DOTA_Tooltip_ability_chaos_knight_reality_rift_armor_reduction": "ARMOR REDUCTION:",
  "DOTA_Tooltip_ability_chaos_knight_reality_rift_armor_duration": "REDUCTION DURATION:",
  "DOTA_Tooltip_ability_chaos_knight_chaos_strike": "Chaos Strike",
  "DOTA_Tooltip_ability_chaos_knight_chaos_strike_Description": "Each of Chaos Knight's attacks has a chance to deal bonus damage, also causing the critical strike to lifesteal for %lifesteal%%% of the damage.",
  "DOTA_Tooltip_ability_chaos_knight_chaos_strike_Lore": "Chaos Knight's unwieldy power bludgeons those who stand before him, crushing the thickest of armors.",
  "DOTA_Tooltip_ability_chaos_knight_chaos_strike_crit_chance": "%CRITICAL CHANCE:",
  "DOTA_Tooltip_ability_chaos_knight_chaos_strike_crit_damage": "%CRITICAL DAMAGE:",
