  "DOTA_Tooltip_ability_chaos_knight_phantasm": "Phantasm",
  "DOTA_Tooltip_ability_chaos_knight_phantasm_Description": "Summons several phantasmal copies of the Chaos Knight from alternate dimensions. There is a 50%% chance an extra phantasm will be summoned. The phantasms deal %outgoing_damage_tooltip%%% damage, but take %incoming_damage_tooltip%%% damage.\\n\\nUpgradeable by Aghanim's Scepter.\\nDISPEL TYPE: Basic Dispel",
  "DOTA_Tooltip_ability_chaos_knight_phantasm_Lore": "Drawing on his battles fought across many worlds and many times, phantasms of the Chaos Knight rise up to quell all who oppose him.",
  "DOTA_Tooltip_ability_chaos_knight_phantasm_Note0": "Removes most negative buffs from Chaos Knight.",
  "DOTA_Tooltip_ability_chaos_knight_phantasm_Note1": "Chaos Knight is invulnerable for 0.5 seconds when casting, allowing him to dodge stuns and damage.",
