  "DOTA_Tooltip_ability_chaos_knight_phantasm_outgoing_damage_tooltip": "%PHANTASM DAMAGE:",
  "DOTA_Tooltip_ability_chaos_knight_phantasm_aghanim_description": "Reduces cooldown to %cooldown_scepter% seconds and allows Phantasm to be cast on an allied hero. 1200 Cast Range",
  "DOTA_Tooltip_ability_chaos_knight_phantasm_cooldown_scepter": "SCEPTER COOLDOWN:",
  "DOTA_Tooltip_ability_ogre_magi_fireblast": "Fireblast",
