  "DOTA_Tooltip_ability_ogre_magi_fireblast_Lore": "The Ogre Magi is easily amused, entertained for hours by playing with fire.",
  "DOTA_Tooltip_ability_ogre_magi_fireblast_Note0": "When Multicast, Fireblast hits the same target each time with a 0.3 seconds interval.",
