  "DOTA_Tooltip_ability_ogre_magi_unrefined_fireblast_Description": "Blasts an enemy unit with a wave of fire, dealing damage and stunning the target. Its mana cost is 60%% of Ogre Magi's current mana.",
  "DOTA_Tooltip_ability_ogre_magi_unrefined_fireblast_Lore": "The 'who-casts-spell-first' argument between himself is solved simply by both casting at once.",
