  "DOTA_Tooltip_ability_ogre_magi_bloodlust_Description": "Incites a frenzy in a friendly unit, increasing its movement speed and attack speed. Can be cast on towers.",
  "DOTA_Tooltip_ability_ogre_magi_bloodlust_Lore": "'Running's not as fun as hitting... Not one bit fun.'",
  "DOTA_Tooltip_ability_ogre_magi_bloodlust_Note0": "When multicast, nearby allies will be selected randomly in an 575 area of effect to receive Bloodlust.",
  "DOTA_Tooltip_ability_ogre_magi_bloodlust_Note1": "Can affect siege units.",
