  "DOTA_Tooltip_ability_treant_natures_guise": "Nature's Guise",
  "DOTA_Tooltip_ability_treant_natures_guise_Description": "Turns Treant invisible after a short delay when he is within close radius of a tree. Increases movement speed while invisible, and causes a disarming root when attacking out of invisibility.",
  "DOTA_Tooltip_ability_treant_natures_guise_Lore": "The Protectors don't often come into vision; their natural state being nestled within their leafy brethren.",
  "DOTA_Tooltip_ability_treant_natures_guise_Note0": "Attack out of invisibilty cannot miss.",
  "DOTA_Tooltip_ability_treant_natures_guise_Note1": "Nature's Guise checks in 0.75 second intervals for nearby trees, removing invisibility if none are found.",
  "DOTA_Tooltip_ability_treant_natures_guise_Note2": "Can cast while invisible.",
