  "DOTA_Tooltip_ability_treant_leech_seed": "Leech Seed",
  "DOTA_Tooltip_ability_treant_leech_seed_Description": "Treant plants a life-sapping seed in an enemy unit, draining its health, while simultaneously slowing it. The seed heals friendly units around it.  Pulses 7 times.",
  "DOTA_Tooltip_ability_treant_leech_seed_Lore": "Rooftrellen nurtures the beings under his stewardship, sustained by the lifeforce of trespassers into his sacred ground.",
  "DOTA_Tooltip_ability_treant_leech_seed_leech_damage": "DAMAGE PER PULSE:",
