  "DOTA_Tooltip_ability_treant_overgrowth": "Overgrowth",
  "DOTA_Tooltip_ability_treant_overgrowth_Description": "Summons an overgrowth of vines and branches around Treant that prevent afflicted enemies from moving, blinking, going invisible, or attacking.",
  "DOTA_Tooltip_ability_treant_overgrowth_Lore": "Rooftrellen calls the ancestral spirit of nature, releasing its power through all of his kin.",
  "DOTA_Tooltip_ability_treant_overgrowth_Note0": "Works on Spell Immune units, but if units gain spell immunity after the effect starts, they'll break free.",
  "DOTA_Tooltip_ability_treant_overgrowth_Note1": "Being entangled by Overgrowth through enchanted trees from Eyes of the Forest does damage. Being entangled by Overgrowth directly from Treant Protector does not.",
  "DOTA_Tooltip_ability_treant_overgrowth_duration": "DURATION:",
  "DOTA_Tooltip_ability_treant_overgrowth_radius": "RADIUS:",
  "DOTA_Tooltip_ability_treant_overgrowth_aghanim_description": "When Overgrowth is cast, it is also cast around Eye of the Forest to deal 175 damage per second.",
  "DOTA_Tooltip_ability_treant_eyes_in_the_forest": "Eyes In The Forest",
  "DOTA_Tooltip_ability_treant_eyes_in_the_forest_Description": "Treant Protector enchants a tree, which grants him unobstructed vision in that location. If Overgrowth is cast, units within a radius of an enchanted tree will be entangled and damaged.",
  "DOTA_Tooltip_ability_treant_eyes_in_the_forest_vision_aoe": "TREE VISION RADIUS:",
  "DOTA_Tooltip_ability_treant_eyes_in_the_forest_overgrowth_aoe": "SCEPTER OVERGROWTH RADIUS:",
  "DOTA_Tooltip_ability_treant_eyes_in_the_forest_damage": "SCEPTER OVERGROWTH DAMAGE PER SEC:",
  "DOTA_Tooltip_ability_treant_eyes_in_the_forest_Note0": "Being entangled by Overgrowth through enchanted trees from Eyes of the Forest does damage. Being entangled by Overgrowth directly from Treant Protector does not.",
  "DOTA_Tooltip_ability_treant_eyes_in_the_forest_Note1": "If the enemy has True Sight, they will be able to see which trees are enchanted.",
  "DOTA_Tooltip_ability_treant_eyes_in_the_forest_Lore": "One can never be too certain who, or what, might need protecting.",
  "DOTA_Tooltip_ability_meepo_earthbind": "Earthbind",
  "DOTA_Tooltip_ability_meepo_earthbind_Description": "Tosses a net at the target point, pinning down all enemy units.  Earthbind prevents invisibility and blink.",
  "DOTA_Tooltip_ability_meepo_earthbind_Lore": "Catching dinner in the Riftshadow Ruins can be quite the task.",
  "DOTA_Tooltip_ability_meepo_earthbind_Note0": "While preventing units from going invisible, Earthbind won't hit units that are already invisible.",
  "DOTA_Tooltip_ability_meepo_earthbind_Note1": "Does not work on Spell Immune units or Roshan.",
