  "DOTA_Tooltip_ability_meepo_geostrike": "Geostrike",
  "DOTA_Tooltip_ability_meepo_geostrike_Description": "Meepo enchants his weapon to deal damage per second, as well as slow the movement speed of the attacked unit.  Geostrikes from multiple Meepos stack.",
  "DOTA_Tooltip_ability_meepo_geostrike_Lore": "Keeping your pack light and having few but versatile tools is the best bet for survival.",
