  "DOTA_Tooltip_ability_meepo_divided_we_stand": "Divided We Stand",
  "DOTA_Tooltip_ability_meepo_divided_we_stand_Description": "Meepo summons an imperfect, semi-autonomous duplicate of himself, which can gain gold and experience as he does and shares his experience, attributes and abilities. However, the clones cannot wield any items but the boots that Meepo himself wears.  If any of the clones die, they all die.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_meepo_divided_we_stand_Lore": "Do I know you?",
  "DOTA_Tooltip_ability_meepo_divided_we_stand_Note0": "Clones can't use Arcane Boots or pick up runes.",
  "DOTA_Tooltip_ability_meepo_divided_we_stand_tooltip_clones": "NUMBER OF MEEPOES:",
  "DOTA_Tooltip_ability_meepo_divided_we_stand_aghanim_description": "Adds an extra Meepo.",
  "DOTA_Tooltip_ability_visage_grave_chill": "Grave Chill",
  "DOTA_Tooltip_ability_visage_grave_chill_Description": "Visage drains the movement and attack speed of the targeted unit, gaining it for itself.",
  "DOTA_Tooltip_ability_visage_grave_chill_Lore": "The warmth of the sun is never felt in the dark cold of the Narrow Maze.",
  "DOTA_Tooltip_ability_visage_grave_chill_Note0": "The two buffs can be removed separately by purge.",
  "DOTA_Tooltip_ability_visage_grave_chill_Note1": "The speed gained is independent of the speed lost on the target.",
