  "DOTA_Tooltip_ability_visage_soul_assumption": "Soul Assumption",
  "DOTA_Tooltip_ability_visage_soul_assumption_Description": "Visage gathers charges of soul essence each time nearby heroes take more than %damage_limit% damage. When the essence is released, it deals base damage as well as damage for each gathered soul charge.",
  "DOTA_Tooltip_ability_visage_soul_assumption_Lore": "The collected souls of the dead reach out to bring another into their fold.",
  "DOTA_Tooltip_ability_visage_soul_assumption_Note0": "Charges are only gained for damage greater than 2 and less than 3000 that is dealt by a player or Roshan.  Self-damage or Soul Assumption damage isn't counted.",
  "DOTA_Tooltip_ability_visage_soul_assumption_Note1": "Soul Assumption cannot be disjointed.",
