  "DOTA_Tooltip_ability_visage_gravekeepers_cloak": "Gravekeeper's Cloak",
  "DOTA_Tooltip_ability_visage_gravekeepers_cloak_Description": "Visage generates a layered barrier that protects him from damage.  If he receives damage from a player, one layer is removed, and takes time to recover.",
  "DOTA_Tooltip_ability_visage_gravekeepers_cloak_Lore": "Visage's tough scales share equal properties of ghost and stone, making it nearly impervious to blade and magical assault.",
  "DOTA_Tooltip_ability_visage_gravekeepers_cloak_Note0": "Each time Visage receives damage greater than 50 that was not self-inflicted and was from a player owned source, a layer is removed.",
  "DOTA_Tooltip_ability_visage_gravekeepers_cloak_damage_reduction": "%DAMAGE REDUCTION PER LAYER:",
