  "DOTA_Tooltip_ability_visage_gravekeepers_cloak_minimum_damage": "MINIMUM DAMAGE:",
  "DOTA_Tooltip_ability_visage_summon_familiars": "Summon Familiars",
  "DOTA_Tooltip_ability_visage_summon_familiars_Description": "Conjures two blind Familiars to fight for Visage.  Familiars possess the Stone Form ability, that allows them to turn into stone, stunning enemies upon landing.  While in Stone Form, Familiars are invulnerable, and rapidly regenerate their health.  Familiars grant high bounty when killed.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_visage_summon_familiars_Lore": "Guardians of the Narrow Maze, Necro'lic's Familiars stand watch over his domain.",
  "DOTA_Tooltip_ability_visage_summon_familiars_Note0": "Casting Summon Familiars will replace any existing Familiars.",
