  "DOTA_Tooltip_ability_visage_stone_form_self_cast_Description": "After a short delay, the Familiar turns into stone and smashes into the ground, stunning and damaging all targets in the area.  The Familiar becomes invulnerable, and will regain its health very rapidly.  After %stone_duration% seconds, the Familiar will automatically leave Stone Form.",
  "DOTA_Tooltip_ability_visage_stone_form_self_cast_stun_duration": "STUN DURATION:",
