  "DOTA_Tooltip_ability_undying_decay": "Decay",
  "DOTA_Tooltip_ability_undying_decay_Description": "Undying steals strength from all enemy heroes in an area, dealing base damage as he claims the enemy's strength for himself.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_undying_decay_Lore": "The strength of the living is simply borrowed from the strength of the dead.",
