  "DOTA_Tooltip_ability_rubick_null_field_Description": "Rubick's mastery of the arcane creates an aura that nullifies nearby enemy senses or increases his own. Can be toggled offensively or defensively to reduce enemy magic resistance or increase allied magic resistance.",
  "DOTA_Tooltip_ability_rubick_null_field_Lore": "Not every magus can be a Grand Magus...",
