  "DOTA_Tooltip_ability_rubick_spell_steal": "Spell Steal",
  "DOTA_Tooltip_ability_rubick_spell_steal_Description": "Rubick studies the trace magical essence of one enemy hero, learning the secrets of the last spell the hero cast. Rubick can use this spell as his own for several minutes or until he dies.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_rubick_spell_steal_Lore": "...but even their lesser magics can be a source of much utility.",
