  "DOTA_Tooltip_ability_disruptor_static_storm_Description": "Creates a damaging static storm that also silences all enemy units in the area for the duration.  The damage starts off weak, but increases in power over the duration.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_disruptor_static_storm_Lore": "A summer squall in Druud is a hardship that only an Oglodi can survive.",
