  "DOTA_Tooltip_ability_nyx_assassin_impale": "Impale",
  "DOTA_Tooltip_ability_nyx_assassin_impale_Description": "Rock spikes burst from the earth along a straight path. Enemy units are hurled into the air, then are stunned and take damage when they fall.",
  "DOTA_Tooltip_ability_nyx_assassin_impale_Lore": "All zealot scarabs possess intimate knowledge of underground pathways, using them to their advantage.",
  "DOTA_Tooltip_ability_nyx_assassin_impale_Note0": "The spikes move at 1600 units per second, over 700 range.",
  "DOTA_Tooltip_ability_nyx_assassin_impale_Note1": "Impale cannot be blocked by Linken's Sphere.",
