  "DOTA_Tooltip_ability_nyx_assassin_mana_burn": "Mana Burn",
  "DOTA_Tooltip_ability_nyx_assassin_mana_burn_Description": "Destroys the target hero's mana equal to a multiplier of its Intelligence, and deals damage equal to the mana burned.",
  "DOTA_Tooltip_ability_nyx_assassin_mana_burn_Lore": "The tome in Ultimyr describes one scarab with the ability eat away at the mind of lesser beings.",
  "DOTA_Tooltip_ability_nyx_assassin_mana_burn_float_multiplier": "INTELLIGENCE MULTIPLIER:",
  "DOTA_Tooltip_ability_nyx_assassin_spiked_carapace": "Spiked Carapace",
  "DOTA_Tooltip_ability_nyx_assassin_spiked_carapace_Description": "When activated while above ground, Spiked Carapace reflects and negates damage dealt to Nyx Assassin (max once from each source), as well as stunning the source of the damage. Activating Spiked Carapace will not break Vendetta invisibility.\\n\\nWhile Burrowed, Spiked Carapace instantly stuns nearby enemies when cast.",
  "DOTA_Tooltip_ability_nyx_assassin_spiked_carapace_Lore": "While their carapace is relatively thin, it's guarded by a retractable field of razor-sharp spikes.",
  "DOTA_Tooltip_ability_nyx_assassin_spiked_carapace_Note0": "The damage is calculated after all reductions.",
  "DOTA_Tooltip_ability_nyx_assassin_spiked_carapace_Note1": "Spiked Carapace will only trigger on damage from other player controlled units.",
  "DOTA_Tooltip_ability_nyx_assassin_spiked_carapace_reflect_duration": "REFLECT DURATION:",
  "DOTA_Tooltip_ability_nyx_assassin_spiked_carapace_stun_duration": "STUN DURATION:",
