  "DOTA_Tooltip_ability_nyx_assassin_vendetta": "Vendetta",
  "DOTA_Tooltip_ability_nyx_assassin_vendetta_Description": "Allows Nyx Assassin to become invisible and gain a speed bonus.  If Nyx Assassin attacks to break the invisibility, massive bonus damage is dealt with the attack.",
  "DOTA_Tooltip_ability_nyx_assassin_vendetta_Lore": "The scarab kills for the glory of his queen.",
  "DOTA_Tooltip_ability_nyx_assassin_vendetta_Note0": "Has a 0 second fadetime.",
  "DOTA_Tooltip_ability_nyx_assassin_vendetta_Note1": "If a spell is used to break the invisibility, or the invisibility expires, the bonus damage is lost.",
  "DOTA_Tooltip_ability_nyx_assassin_vendetta_duration": "DURATION:",
  "DOTA_Tooltip_ability_nyx_assassin_vendetta_movement_speed": "%MOVEMENT SPEED:",
  "DOTA_Tooltip_ability_nyx_assassin_vendetta_bonus_damage": "BONUS DAMAGE:",
  "DOTA_Tooltip_ability_nyx_assassin_burrow": "Burrow",
  "DOTA_Tooltip_ability_nyx_assassin_burrow_Description": "Nyx Assassin buries himself beneath the battlefield over a short duration. Once burrowed, Spiked Carapace instantly stuns nearby enemies when cast, the range of Mana Burn and Impale is increased, and Impale's cooldown is decreased. While burrowed, Nyx Assassin is stationary, unable to attack, and invisible. His health and mana regeneration are also increased, and he gains damage reduction from all damage sources.",
  "DOTA_Tooltip_ability_nyx_assassin_burrow_Lore": "Some castes of zealot scarab are known to create small burrows to lie in wait, ready to ambush their prey.",
  "DOTA_Tooltip_ability_nyx_assassin_burrow_Note0": "Casting Vendetta will cancel burrow.",
