  "DOTA_Tooltip_ability_nyx_assassin_unburrow_Lore": "Some castes of zealot scarab are known to create small burrows to lie in wait, ready to ambush their prey.",
  "DOTA_Tooltip_ability_naga_siren_mirror_image": "Mirror Image",
  "DOTA_Tooltip_ability_naga_siren_mirror_image_Description": "Creates multiple images of Naga Siren under her control.\\n\\nDISPEL TYPE: Basic Dispel",
  "DOTA_Tooltip_ability_naga_siren_mirror_image_Lore": "Slithice, while being strong in her own right, is notoriously hard to track down.",
  "DOTA_Tooltip_ability_naga_siren_mirror_image_Note0": "Naga Siren is invulnerable for 0.3 seconds when casting, allowing her to dodge stuns and damage.",
  "DOTA_Tooltip_ability_naga_siren_mirror_image_Note1": "Upon casting, most effects are removed from Naga Siren.",
