  "DOTA_Tooltip_ability_naga_siren_ensnare": "Ensnare",
  "DOTA_Tooltip_ability_naga_siren_ensnare_Description": "Interrupts the target and traps them in place, preventing movement or blinking.",
  "DOTA_Tooltip_ability_naga_siren_ensnare_Lore": "The only way to escape the onslaught of a Slithereen is to never oppose one in the first place.",
  "DOTA_Tooltip_ability_naga_siren_ensnare_Note0": "Works on Spell Immune units.",
  "DOTA_Tooltip_ability_naga_siren_ensnare_duration": "DURATION:",
  "DOTA_Tooltip_ability_naga_siren_rip_tide": "Rip Tide",
  "DOTA_Tooltip_ability_naga_siren_rip_tide_Description": "The Naga Siren and her images hit all nearby units with a damaging wave of water that lowers armor for 8 seconds.",
  "DOTA_Tooltip_ability_naga_siren_rip_tide_Lore": "A torrent from the Deep Ones crushes the defenses of the Siren's enemies.",
  "DOTA_Tooltip_ability_naga_siren_rip_tide_Note0": "Naga's illusions will also cast Rip Tide, but enemies in multiple areas of effect will only be hit once.",
  "DOTA_Tooltip_ability_naga_siren_rip_tide_armor_reduction": "ARMOR REDUCTION:",
  "DOTA_Tooltip_ability_naga_siren_rip_tide_duration": "DURATION:",
  "DOTA_Tooltip_ability_naga_siren_song_of_the_siren": "Song of the Siren",
  "DOTA_Tooltip_ability_naga_siren_song_of_the_siren_Description": "All enemies in range of the Naga Siren are put into a magical stasis where they cannot act or be attacked. Using Song of the Siren again will end the duration early.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_naga_siren_song_of_the_siren_Lore": "Slithice's powerful voice enchants leagues of opponents, while calling her Slithereen kin in a time of need.",
  "DOTA_Tooltip_ability_naga_siren_song_of_the_siren_Note0": "Units affected by Song of the Siren are invulnerable and cannot take damage from any source.",
  "DOTA_Tooltip_ability_naga_siren_song_of_the_siren_Note1": "Doesn't affect Spell Immune units.",
  "DOTA_Tooltip_ability_naga_siren_song_of_the_siren_Note2": "The area of the song follows Naga, so if she moves far enough away from affected units, they will wake up.",
  "DOTA_Tooltip_ability_naga_siren_song_of_the_siren_aghanim_description": "Song of the Siren regenerates all nearby allies for 10% of their health per second.",
  "DOTA_Tooltip_ability_naga_siren_song_of_the_siren_duration": "DURATION:",
  "DOTA_Tooltip_ability_naga_siren_song_of_the_siren_radius": "RADIUS:",
  "DOTA_Tooltip_ability_naga_siren_song_of_the_siren_regen_rate_tooltip_scepter": "%SCEPTER HEALTH REGEN:",
  "DOTA_Tooltip_ability_naga_siren_song_of_the_siren_cancel": "Song of the Siren End",
  "DOTA_Tooltip_ability_naga_siren_song_of_the_siren_cancel_Description": "Release enemy units from your song so they can be targeted again.",
  "DOTA_Tooltip_ability_keeper_of_the_light_illuminate": "Illuminate",
  "DOTA_Tooltip_ability_keeper_of_the_light_illuminate_Description": "Channels light energy, building power the longer it's channeled.  Once released, a wave is sent forth that deals damage and gives vision in its path.  The longer it is channeled, the more damage is dealt.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_keeper_of_the_light_illuminate_Lore": "Ezalor's hidden light reveals itself in marvelous fashion.",
  "DOTA_Tooltip_ability_keeper_of_the_light_illuminate_Note0": "Gives increasing vision over the channeling time.",
  "DOTA_Tooltip_ability_keeper_of_the_light_illuminate_Note1": "Damages mechanical units.",
  "DOTA_Tooltip_ability_keeper_of_the_light_illuminate_damage_per_second": "DAMAGE PER SECOND CHANNELED:",
