  "DOTA_Tooltip_ability_keeper_of_the_light_illuminate_end_Description": "Release the channel early.",
  "DOTA_Tooltip_ability_keeper_of_the_light_mana_leak": "Mana Leak",
  "DOTA_Tooltip_ability_keeper_of_the_light_mana_leak_Description": "Weakens an enemy's magical essence, causing them to lose mana as they move.  If the enemy loses all of its mana, it will be stunned.",
  "DOTA_Tooltip_ability_keeper_of_the_light_mana_leak_Lore": "Ezalor disrupts Primordial harmony, draining magical energy from those with poor constitution.",
  "DOTA_Tooltip_ability_keeper_of_the_light_mana_leak_Note0": "If the target travels over 300 distance in less than 0.1 second (by using blink skills, for example), Mana Leak will not decrease its mana for that distance.",
  "DOTA_Tooltip_ability_keeper_of_the_light_mana_leak_mana_leak_pct": "%MANA LEAK PERCENT:",
  "DOTA_Tooltip_ability_keeper_of_the_light_mana_leak_duration": "LEAK DURATION:",
