  "DOTA_Tooltip_ability_keeper_of_the_light_chakra_magic": "Chakra Magic",
  "DOTA_Tooltip_ability_keeper_of_the_light_chakra_magic_Description": "Restores mana to the target unit, increases its mana capacity, and reduces the cooldown of the next spell they cast.",
  "DOTA_Tooltip_ability_keeper_of_the_light_chakra_magic_Lore": "In the same vein, Ezalor bestows his harmony among others.",
