  "DOTA_Tooltip_ability_keeper_of_the_light_spirit_form": "Spirit Form",
  "DOTA_Tooltip_ability_keeper_of_the_light_spirit_form_Description": "Ezalor temporarily turns his body luminescent, gaining various abilities.  Illuminate is now channeled by a separate spirit, and gains the Blinding Light and Recall abilities.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_keeper_of_the_light_spirit_form_Lore": "In a flash of light, Ezalor reveals his true nature.",
  "DOTA_Tooltip_ability_keeper_of_the_light_spirit_form_duration": "DURATION:",
  "DOTA_Tooltip_ability_keeper_of_the_light_spirit_form_aghanim_description": "Permanent Spirit Form. During daytime, grants unobstructed vision, and allows Illuminate to heal allies for the same amount as its damage values.",
  "DOTA_Tooltip_ability_keeper_of_the_light_recall": "Recall",
  "DOTA_Tooltip_ability_keeper_of_the_light_recall_Description": "After a short delay, teleports the targeted friendly hero to your location.  If the targeted friendly hero takes player based damage during this time, the ability is interrupted.",
  "DOTA_Tooltip_ability_keeper_of_the_light_recall_Lore": "Walk towards the light.",
  "DOTA_Tooltip_ability_keeper_of_the_light_recall_Note0": "If Keeper of the Light dies, the spell effect is interrupted.",
  "DOTA_Tooltip_ability_keeper_of_the_light_recall_Note1": "Recall will interrupt channeling spells.",
  "DOTA_Tooltip_ability_keeper_of_the_light_recall_Note2": "You can disable help to prevent Keeper from casting this spell on you.",
