  "DOTA_Tooltip_ability_keeper_of_the_light_spirit_form_illuminate": "Illuminate",
  "DOTA_Tooltip_ability_keeper_of_the_light_spirit_form_illuminate_Description": "Channels light energy, building power the longer it's channeled.  Once released, a wave is sent forth that deals damage and gives vision in its path.  The longer it is channeled, the more damage is dealt.\\n\\nUpgradable by Aghanim's Scepter.",
