  "DOTA_Tooltip_ability_wisp_tether": "Tether",
  "DOTA_Tooltip_ability_wisp_tether_Description": "Tethers Io to an allied unit, granting bonus movement speed to both. When Io restores health or mana, tethered units target gains a larger portion of that amount. The tether breaks when the allied unit moves too far away, or Io cancels the tether.",
  "DOTA_Tooltip_ability_wisp_tether_Lore": "The benevolent touch of Io brings strength from between the planes.",
  "DOTA_Tooltip_ability_wisp_tether_Note0": "If you try to tether a unit that is 700 distance or further away from Io, he will latch on and pull himself closer to the tethered unit (to a distance of 300).",
  "DOTA_Tooltip_ability_wisp_tether_Note1": "The tethered unit will heal faster.",
