  "DOTA_Tooltip_ability_wisp_tether_break_Description": "Break the link to the tethered unit.",
  "DOTA_Tooltip_ability_wisp_spirits": "Spirits",
  "DOTA_Tooltip_ability_wisp_spirits_Description": "Summon five particle spirits that dance in a circle around Io. If a particle collides with an enemy hero, it explodes, damaging and slowing all enemy units in an area around it.  Creeps take minor damage from touching a particle spirit, but do not cause them to explode.  When its duration ends, any remaining Spirits explode.",
  "DOTA_Tooltip_ability_wisp_spirits_Lore": "Io twists the particles of the universe with its unimaginable capabilities.",
