  "DOTA_Tooltip_ability_wisp_spirits_explode_radius": "EXPLOSION RADIUS:",
  "DOTA_Tooltip_ability_wisp_spirits_spirit_duration": "DURATION:",
  "DOTA_Tooltip_ability_wisp_spirits_in": "Spirits Movement",
  "DOTA_Tooltip_ability_wisp_spirits_in_Description": "Calls the spirits in and out.",
  "DOTA_Tooltip_ability_wisp_spirits_out": "Spirits Out",
  "DOTA_Tooltip_ability_wisp_spirits_out_Description": "Sends the spirits farther away from you. Can be toggled on and off.",
  "DOTA_Tooltip_ability_wisp_overcharge": "Overcharge",
  "DOTA_Tooltip_ability_wisp_overcharge_Lore": "Drawing on the energy of matter from all worlds, Io begins the unravelling of time.",
  "DOTA_Tooltip_ability_wisp_overcharge_Description": "Io gains bonus attack speed and damage reduction, at the cost of draining a percentage of its current health and mana per second.  If Io is Tethered to an ally, that unit also gains the bonuses.",
  "DOTA_Tooltip_ability_wisp_overcharge_Note0": "Io's tethered unit will also benefit from the attack speed and reduction, but only Io pays the drain in HP and mana.",
  "DOTA_Tooltip_ability_wisp_overcharge_bonus_attack_speed": "ATTACK SPEED:",
  "DOTA_Tooltip_ability_wisp_overcharge_bonus_damage_pct": "%INCOMING DAMAGE REDUCTION:",
  "DOTA_Tooltip_ability_wisp_overcharge_drain_pct_tooltip": "%HEALTH/MANA DRAIN:",
  "DOTA_Tooltip_ability_wisp_relocate": "Relocate",
  "DOTA_Tooltip_ability_wisp_relocate_Lore": "Io is the embodiment of the mystery of the universe.",
  "DOTA_Tooltip_ability_wisp_relocate_Description": "Teleports Io and any tethered ally to any location. After the spell expires Io and any tethered ally will return to their original location. Double-click to teleport to your team's base fountain.",
  "DOTA_Tooltip_ability_wisp_relocate_Note0": "Enemies receive a visual indicator in the world and on the minimap of the target location during the cast delay.",
  "DOTA_Tooltip_ability_wisp_relocate_Note1": "If Io is disabled during the cast delay, Relocate is cancelled.",
  "DOTA_Tooltip_ability_wisp_relocate_Note2": "If an allied hero is Tethered, that hero will teleported along with you. You can break the tether at any time to prevent that hero from teleporting with you.",
