  "DOTA_Tooltip_ability_slark_pounce": "Pounce",
  "DOTA_Tooltip_ability_slark_pounce_Description": "Slark leaps forward, grabbing the first hero he connects with.  That unit takes damage and is leashed to Slark, and can only move a limited distance away from Slark's landing position.",
  "DOTA_Tooltip_ability_slark_pounce_Lore": "Time in the Dark Reef made Slark a dangerous assassin; aggressive and fearless.",
  "DOTA_Tooltip_ability_slark_pounce_Note0": "On cast, Slark leaps forward at a speed of 933.33, stopping when he latches onto an enemy hero or has traveled 700 distance.",
  "DOTA_Tooltip_ability_slark_pounce_Note1": "Spell Immune units cannot be leashed.",
  "DOTA_Tooltip_ability_slark_pounce_Note2": "Trees will be destroyed around Slark's landing location.",
  "DOTA_Tooltip_ability_slark_pounce_Note3": "Blinking or teleporting can break the leash.",
