  "DOTA_Tooltip_ability_slark_essence_shift_duration": "DURATION:",
  "DOTA_Tooltip_ability_slark_shadow_dance": "Shadow Dance",
  "DOTA_Tooltip_ability_slark_shadow_dance_Description": "When used, Slark hides himself in a cloud of shadows, becoming immune to detection. Attacking, casting spells, and using items will not reveal Slark.  Passively, when not visible to the enemy team, Slark gains bonus movement speed and health regeneration.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_slark_shadow_dance_aghanim_description": "Reduces cooldown and causes Shadow Dance active to be an area of effect ability, hiding nearby allied heroes underneath it.",
  "DOTA_Tooltip_ability_slark_shadow_dance_Lore": "The hidden Thirteenth is a slippery foe.",
  "DOTA_Tooltip_ability_slark_shadow_dance_Note0": "If Slark is damaged by a neutral unit, the passive movement and health regeneration bonuses are lost for 2 seconds.",
  "DOTA_Tooltip_ability_slark_shadow_dance_Note1": "The passive movement and health regeneration bonuses have a 0.5 second activation and deactivation delay.",
