  "DOTA_Tooltip_ability_medusa_split_shot": "Split Shot",
  "DOTA_Tooltip_ability_medusa_split_shot_Description": "Medusa magically splits her shot into several arrows. These arrows deal a lower percent of her normal damage.\\n\\nThe extra targets will not receive other attack effects (such as critical strike).",
  "DOTA_Tooltip_ability_medusa_split_shot_Lore": "All who encounter the cursed Gorgon will feel her rage.",
  "DOTA_Tooltip_ability_medusa_split_shot_Note0": "Secondary targets will be the closest enemy units excluding the primary target.",
  "DOTA_Tooltip_ability_medusa_split_shot_Note1": "If illusions of Medusa are created while Split Shot is active, they will also have this ability.",
  "DOTA_Tooltip_ability_medusa_split_shot_damage_modifier_tooltip": "%OUTGOING DAMAGE:",
  "DOTA_Tooltip_ability_medusa_split_shot_arrow_count": "EXTRA TARGETS:",
  "DOTA_Tooltip_ability_medusa_mystic_snake": "Mystic Snake",
  "DOTA_Tooltip_ability_medusa_mystic_snake_Description": "A mystic snake made of energy jumps from target to target dealing damage and stealing mana. After it reaches its last target, it returns to Medusa to replenish her with mana. The snake deals more damage per jump and steals %snake_mana_steal%%% of the targets' total mana. Deals Pure damage to units petrified by Stone Gaze.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_medusa_mystic_snake_Lore": "The Gorgon's curse is her greatest strength.",
  "DOTA_Tooltip_ability_medusa_mystic_snake_Note0": "Mystic Snake cannot be disjointed.",
  "DOTA_Tooltip_ability_medusa_mystic_snake_radius": "JUMP RADIUS:",
