  "DOTA_Tooltip_ability_medusa_mystic_snake_snake_mana_steal": "%MANA STEAL:",
  "DOTA_Tooltip_ability_medusa_mystic_snake_aghanim_description": "Causes Mystic Snake to turn enemies into stone for %stone_form_scepter_base% second, increases by  %stone_form_scepter_increment% seconds per bounce.",
  "DOTA_Tooltip_ability_medusa_mana_shield": "Mana Shield",
  "DOTA_Tooltip_ability_medusa_mana_shield_Description": "Creates a shield that absorbs %absorption_tooltip%%% of the incoming damage in exchange for Medusa's mana.",
  "DOTA_Tooltip_ability_medusa_mana_shield_Lore": "While Medusa was not gifted with eternal life upon birth, her curse bestowed her with impressive defensive powers.",
  "DOTA_Tooltip_ability_medusa_mana_shield_Note0": "Absorbs damage before any damage reduction takes place, meaning that armor will not reduce the amount of mana needed to absorb damage.",
  "DOTA_Tooltip_ability_medusa_mana_shield_damage_per_mana": "DAMAGE PER MANA:",
  "DOTA_Tooltip_ability_medusa_stone_gaze": "Stone Gaze",
  "DOTA_Tooltip_ability_medusa_stone_gaze_Description": "Any enemy units looking at Medusa will have their movement and attack speed slowed.  If 2 seconds of total time is accumulated looking at Medusa while Stone Gaze is active, that unit will turn to stone.  Petrified units are stunned, have 100%% magic damage resistance, and take bonus physical damage.  If the petrified unit is an illusion, it is immediately killed.",
  "DOTA_Tooltip_ability_medusa_stone_gaze_Lore": "The beauty of Medusa is legendary.",
  "DOTA_Tooltip_ability_medusa_stone_gaze_Note0": "Affects Spell Immune units.",
  "DOTA_Tooltip_ability_medusa_stone_gaze_Note1": "Regardless of when a unit receives the Stone Gaze buff while the ability is active on Medusa, it will still receive the full debuff duration.",
