  "DOTA_Tooltip_ability_troll_warlord_berserkers_rage": "Berserker's Rage",
  "DOTA_Tooltip_ability_troll_warlord_berserkers_rage_Description": "While active, Troll Warlord swaps from using ranged to melee attacks. Melee attacks have a chance to bash, dealing bonus damage and stunning the target. Berserker's Rage also grants additional movement speed, armor, and increased attack speed.\\n\\nWhirling Axes has different functionality while Berserker's Rage is active.",
  "DOTA_Tooltip_ability_troll_warlord_berserkers_rage_Lore": "Like his anger, Troll Warlord's supply of axes is infinite.",
  "DOTA_Tooltip_ability_troll_warlord_berserkers_rage_Note0": "The bash stun goes through spell immunity.",
  "DOTA_Tooltip_ability_troll_warlord_berserkers_rage_Note1": "Doesn't stack with Skull Basher.",
  "DOTA_Tooltip_ability_troll_warlord_berserkers_rage_Note2": "Manta Style's cooldown time will be based on whether Troll Warlord was in his ranged or melee form when the item was used.",
