  "DOTA_Tooltip_ability_centaur_double_edge": "Double Edge",
  "DOTA_Tooltip_ability_centaur_double_edge_Description": "Centaur strikes a mighty blow at melee range, damaging both himself and a small area around the target.  Centaur cannot die from Double Edge.",
  "DOTA_Tooltip_ability_centaur_double_edge_Lore": "In the spurs of combat, Bradwarden's vicious strikes sometimes cause self-inflicted collateral damage.",
