  "DOTA_Tooltip_ability_centaur_stampede_Description": "Centaur leads all allies into a vicious charge causing them to move through units at max speed and stop any enemy units they trample. Trampled enemies take damage based on Centaur Warrunner's strength. Each enemy can be trampled once.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_centaur_stampede_Lore": "The great belt of Omexe, which labels Bradwarden as the greatest warrior of his kind, incites his fellow gladiators to follow him into barbarous combat.",
