  "DOTA_Tooltip_ability_skywrath_mage_arcane_bolt": "Arcane Bolt",
  "DOTA_Tooltip_ability_skywrath_mage_arcane_bolt_Description": "Skywrath Mage launches a slow-moving bolt of arcane magic, dealing damage to an enemy unit based on Skywrath Mage's intelligence.",
  "DOTA_Tooltip_ability_skywrath_mage_arcane_bolt_Lore": "Within the Ghastly Eyrie's endless intrigue, only the clever and calm can hope to survive.",
