  "DOTA_Tooltip_ability_skywrath_mage_mystic_flare_Lore": "Only the most practiced of Skywrath sorcerers could hope to shape the skies into such a storm.",
  "DOTA_Tooltip_ability_skywrath_mage_mystic_flare_Note0": "Mystic Flare only affects Heroes; it does not damage illusions or creep heroes.",
