  "DOTA_Tooltip_ability_elder_titan_echo_stomp_spirit_Description": "Elder Titan and his Astral Spirit both stomp the ground, damaging and knocking nearby enemy units unconscious around their locations.  The Elder Titan stomp deals physical damage, while the Spirit stomp deals magical damage.  Unconscious enemies will wake if they take damage.",
  "DOTA_Tooltip_ability_elder_titan_echo_stomp_spirit_Lore": "The force of creation still echoes in the stomp of the Titan.",
