  "DOTA_Tooltip_ability_elder_titan_ancestral_spirit": "Astral Spirit",
  "DOTA_Tooltip_ability_elder_titan_ancestral_spirit_Description": "Elder Titan sends forth his Astral Spirit, damaging any units it passes through.  When the spirit rejoins the Titan, it grants bonus damage and movement speed for each unit it damaged.\\n\\nThe Astral Spirit possesses the Echo Stomp, Return Spirit, and Natural Order abilities.",
  "DOTA_Tooltip_ability_elder_titan_ancestral_spirit_Lore": "Like the four fundamentals, Elder Titan exists across all planes at once and can draw other aspects of himself to assist in times of need.",
