  "DOTA_Tooltip_ability_elder_titan_natural_order_Description": "Reduces all elements to their basic levels, removing base armor and magic damage resistance from nearby enemy units. The armor reduction is centered around the hero, while the magic armor reduction is centered around Astral Spirit.",
  "DOTA_Tooltip_ability_elder_titan_natural_order_Lore": "As it was at the beginning, Elder Titan makes it so again.",
  "DOTA_Tooltip_ability_elder_titan_natural_order_Note0": "When the Astral Spirit isn't present, both the armor and magic armor reduction is centered around Elder Titan.",
