  "DOTA_Tooltip_ability_elder_titan_earth_splitter_Description": "Sends forth a jagged crack in front of Elder Titan. After 3 seconds, the crack implodes, slowing movement while dealing damage to each enemy based on their maximum life. Half of the damage dealt is Magical damage, while the other half is Physical damage.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_elder_titan_earth_splitter_Lore": "That which he created, the titan tears asunder.",
