  "DOTA_Tooltip_ability_legion_commander_overwhelming_odds": "Overwhelming Odds",
  "DOTA_Tooltip_ability_legion_commander_overwhelming_odds_Description": "Turns the enemies' numbers against them, dealing damage and granting you bonus movement speed per unit or per hero.  Deals bonus damage to illusions and summoned units as a percent of their current health.",
