  "DOTA_Tooltip_ability_legion_commander_press_the_attack_Description": "Removes debuffs and disables from the target friendly unit, and grants bonus attack speed and health regen for a short time.\\n\\nDISPEL TYPE: Strong Dispel",
  "DOTA_Tooltip_ability_legion_commander_press_the_attack_Lore": "The rallying horn of the Bronze Legion is able to inspire any heart.",
