  "DOTA_Tooltip_ability_legion_commander_duel": "Duel",
  "DOTA_Tooltip_ability_legion_commander_duel_Description": "Legion Commander and the target enemy hero are forced to attack each other for a short duration.  Neither hero can use items or abilities.  If either hero dies during the duration, the hero winning the Duel gains permanent bonus damage.\\n\\nUpgradable by Aghanim's Scepter.",
  "DOTA_Tooltip_ability_legion_commander_duel_duration": "DURATION:",
