  "DOTA_Tooltip_ability_legion_commander_duel_Note0": "Duels will end if the units are pushed 2000 units apart.",
  "DOTA_Tooltip_ability_legion_commander_duel_Lore": "To face a soldier of Stonehall in single combat is a challenge few can resist.",
  "DOTA_Tooltip_ability_ember_spirit_searing_chains": "Searing Chains",
