  "DOTA_Tooltip_ability_ember_spirit_searing_chains_Note1": "Does not affect Spell Immune or invisible units.",
  "DOTA_Tooltip_ability_ember_spirit_searing_chains_Note2": "Interrupts channeling spells.",
  "DOTA_Tooltip_ability_ember_spirit_searing_chains_Lore": "Xin's harshest lessons often employed the use of red-hot chains.",
  "DOTA_Tooltip_ability_ember_spirit_sleight_of_fist": "Sleight of Fist",
  "DOTA_Tooltip_ability_ember_spirit_sleight_of_fist_Description": "Ember Spirit dashes around with blazing speed, attacking all enemies in the targeted area of effect, then returning to his start location.  Deals bonus damage to heroes, and less damage to creeps.",
  "DOTA_Tooltip_ability_ember_spirit_sleight_of_fist_radius": "RADIUS:",
