  "DOTA_Tooltip_ability_ember_spirit_sleight_of_fist_Note2": "Any attack modifier that Xin has (such as Critical Strike, Bash, Cleave, or Orb effects) will be applied normally on these attacks.",
  "DOTA_Tooltip_ability_ember_spirit_sleight_of_fist_Note3": "Abilities and items can be used while Sleight of Fist is active.",
  "DOTA_Tooltip_ability_ember_spirit_sleight_of_fist_Lore": "The studied warrior must whip and weave through its enemies, burning each without pause.",
  "DOTA_Tooltip_ability_ember_spirit_flame_guard": "Flame Guard",
  "DOTA_Tooltip_ability_ember_spirit_flame_guard_Description": "Ember Spirit surrounds himself with a ring of fire that consumes incoming magic damage, leaving him unharmed.  Flame Guard deals damage per second in an area around Ember Spirit while Flame Guard is active.  If the shield is broken, the damage is also lost.",
