  "DOTA_Tooltip_ability_ember_spirit_flame_guard_Lore": "An enemy should never be allowed to approach without difficulty.",
  "DOTA_Tooltip_ability_ember_spirit_activate_fire_remnant": "Activate Fire Remnant",
  "DOTA_Tooltip_ability_ember_spirit_activate_fire_remnant_Description": "Select the Fire Remnant to arrive at.",
  "DOTA_Tooltip_ability_ember_spirit_activate_fire_remnant_Lore": "With blazing speed does a spirit fly!",
  "DOTA_Tooltip_ability_ember_spirit_fire_remnant": "Fire Remnant",
  "DOTA_Tooltip_ability_ember_spirit_fire_remnant_Description": "Ember Spirit generates Fire Remnant charges every %charge_restore_time% seconds, with a max of %max_charges% charges. Releasing a charge sends a Fire Remnant that runs to the target location at 2.5x Ember Spirit's speed.  Using Activate Fire Remnant, Ember Spirit can dash out to his Remnants, exploding them for area of effect damage.  The targeted Remnant will be arrived at last.",
  "DOTA_Tooltip_ability_ember_spirit_fire_remnant_max_charges": "MAX CHARGES:",
