  "DOTA_Tooltip_ability_ember_spirit_fire_remnant_Lore": "By the spirit's power are Xin's teachings spread anew.",
  "DOTA_Tooltip_ability_earth_spirit_boulder_smash": "Boulder Smash",
  "DOTA_Tooltip_ability_earth_spirit_boulder_smash_Description": "Earth Spirit smashes the target enemy or ally, sending them in the direction he is facing. If Earth Spirit targets an area, he will smash the nearest Stone Remnant in a %remnant_smash_radius_tooltip% radius. The travelling unit or Remnant damages all enemy units it hits.  If an enemy is hit by a Stone Remnant, they are also stunned. Stone Remnants travel further than other units.",
  "DOTA_Tooltip_ability_earth_spirit_boulder_smash_Lore": "It is with the power of a mountain that Earth Spirit strikes his enemies.",
  "DOTA_Tooltip_ability_earth_spirit_boulder_smash_rock_search_aoe": "REMNANT SMASH RADIUS:",
  "DOTA_Tooltip_ability_earth_spirit_boulder_smash_rock_damage": "DAMAGE:",
  "DOTA_Tooltip_ability_earth_spirit_boulder_smash_stun_duration": "STUN DURATION:",
  "DOTA_Tooltip_ability_earth_spirit_boulder_smash_speed": "TRAVEL SPEED:",
  "DOTA_Tooltip_ability_earth_spirit_boulder_smash_unit_distance": "DISTANCE:",
  "DOTA_Tooltip_ability_earth_spirit_boulder_smash_rock_distance": "DISTANCE (REMNANT):",
  "DOTA_Tooltip_ability_earth_spirit_boulder_smash_Note0": "Stun is only applied if the initial target is a Stone Remnant.",
  "DOTA_Tooltip_ability_earth_spirit_boulder_smash_Note1": "Boulder Smash's cooldown is not reset if it fails to knock back a unit or Remnant.",
  "DOTA_Tooltip_ability_earth_spirit_rolling_boulder": "Rolling Boulder",
  "DOTA_Tooltip_ability_earth_spirit_rolling_boulder_Description": "Earth Spirit gathers himself into a boulder and, after a %delay%s delay, rolls toward the target location, damaging enemy units. He will stop if he collides with an enemy hero or is stunned. If he rolls over a Stone Remnant, he will travel further and faster, and enemies hit by the boulder will have their movement speed slowed.",
  "DOTA_Tooltip_ability_earth_spirit_rolling_boulder_Lore": "Calling upon his connection to the land, the Earth Spirit draws loose stone to him, which forms a protective ball that he can use to roll short distances.",
  "DOTA_Tooltip_ability_earth_spirit_rolling_boulder_speed": "SPEED:",
  "DOTA_Tooltip_ability_earth_spirit_rolling_boulder_rock_speed": "SPEED (REMNANT):",
  "DOTA_Tooltip_ability_earth_spirit_rolling_boulder_damage": "DAMAGE:",
  "DOTA_Tooltip_ability_earth_spirit_rolling_boulder_distance": "DISTANCE:",
  "DOTA_Tooltip_ability_earth_spirit_rolling_boulder_rock_distance": "DISTANCE (REMNANT):",
  "DOTA_Tooltip_ability_earth_spirit_rolling_boulder_slow_duration": "SLOW DURATION:",
  "DOTA_Tooltip_ability_earth_spirit_rolling_boulder_move_slow": "%MOVEMENT SLOW:",
  "DOTA_Tooltip_ability_earth_spirit_rolling_boulder_Note0": "Rolling over a Stone Remnant will destroy the Remnant.",
  "DOTA_Tooltip_ability_earth_spirit_rolling_boulder_Note1": "The boulder has a radius of 150.",
  "DOTA_Tooltip_ability_earth_spirit_geomagnetic_grip": "Geomagnetic Grip",
  "DOTA_Tooltip_ability_earth_spirit_geomagnetic_grip_Description": "Earth Spirit pulls the target Stone Remnant. Enemies struck by the gripped target will be silenced, and take damage if the gripped target is a Stone Remnant.",
  "DOTA_Tooltip_ability_earth_spirit_geomagnetic_grip_Note0": "Will not work on a unit inside Chronosphere, Duel, or Black Hole.",
  "DOTA_Tooltip_ability_earth_spirit_geomagnetic_grip_Lore": "Like calls to like. Even the minerals found in the blood and bones of living beings are not immune to the call of the Earth Spirit.",
  "DOTA_Tooltip_ability_earth_spirit_geomagnetic_grip_miss_duration": "SILENCE DURATION:",
  "DOTA_Tooltip_ability_earth_spirit_geomagnetic_grip_rock_damage": "REMNANT DAMAGE:",
  "DOTA_Tooltip_ability_earth_spirit_geomagnetic_grip_pull_units_per_second": "REMNANT PULL SPEED:",
  "DOTA_Tooltip_ability_earth_spirit_geomagnetic_grip_pull_units_per_second_heroes": "HERO PULL SPEED:",
  "DOTA_Tooltip_ability_earth_spirit_stone_caller": "Stone Remnant",
  "DOTA_Tooltip_ability_earth_spirit_stone_caller_Description": "Call a Stone Remnant to the target location.  Stone Remnants have no vision and are invulnerable, and can be used with Earth Spirit's abilities.  Calling a Stone Remnant consumes a charge, which recharge over time.",
  "DOTA_Tooltip_ability_earth_spirit_stone_caller_max_charges": "MAX CHARGES:",
  "DOTA_Tooltip_ability_earth_spirit_stone_caller_charge_restore_time": "CHARGE RESTORE TIME:",
  "DOTA_Tooltip_ability_earth_spirit_stone_caller_duration": "DURATION:",
  "DOTA_Tooltip_ability_earth_spirit_stone_caller_Lore": "Earth Spirit calls forth a remnant of his stone army, long buried with him in the dark embrace of the Earth.",
  "DOTA_Tooltip_ability_earth_spirit_magnetize": "Magnetize",
  "DOTA_Tooltip_ability_earth_spirit_magnetize_Description": "Magnetizes units in a small nearby area, causing them to take damage for a short duration. Magnetized heroes cause nearby Stone Remnants to explode, destroying the remnant and refreshing Magnetize's duration on all nearby enemies. This process can repeat multiple times. If an enemy hero is affected by silence or slows as a result of Geomagnetic Grip or Rolling Boulder, all magnetized heroes share the effects.",
  "DOTA_Tooltip_ability_earth_spirit_magnetize_cast_radius": "RADIUS:",
  "DOTA_Tooltip_ability_earth_spirit_magnetize_damage_per_second": "DAMAGE PER SECOND:",
  "DOTA_Tooltip_ability_earth_spirit_magnetize_damage_duration": "MAGNETIZE DURATION:",
  "DOTA_Tooltip_ability_earth_spirit_magnetize_rock_search_radius": "REMNANT REFRESH RADIUS:",
  "DOTA_Tooltip_ability_earth_spirit_magnetize_rock_explosion_radius": "REMNANT EXPLOSION RADIUS:",
  "DOTA_Tooltip_ability_earth_spirit_magnetize_Lore": "At the Earth Spirit's call, the minerals in the blood and bones of his enemies rebel against the bodies they find themselves in.",
  "DOTA_Tooltip_ability_earth_spirit_petrify": "Enchant Remnant",
  "DOTA_Tooltip_ability_earth_spirit_petrify_Description": "Earth Spirit temporarily enchants a hero, granting them the properties of a Stone Remnant. After a short duration the remnant shatters, releasing the hero and damaging nearby enemies.",
  "DOTA_Tooltip_ability_earth_spirit_petrify_duration": "REMNANT DURATION:",
  "DOTA_Tooltip_ability_earth_spirit_petrify_damage": "SHATTER DAMAGE:",
