  "DOTA_Tooltip_ability_oracle_purifying_flames": "Purifying Flames",
  "DOTA_Tooltip_ability_oracle_purifying_flames_Description": "Burns away impurities, dealing heavy magic damage to the target before causing them to regenerate health over time. The amount of health regenerated over its duration exceeds the amount of initial damage. Can be cast on enemies and allies.\\n\\nUpgradable by Aghanim's Scepter.",
