  "DOTA_Tooltip_ability_oracle_false_promise_Description": "Temporarily alters an ally's destiny, delaying any healing or damage taken until False Promise ends. Any healing that is delayed by False Promise is doubled. Removes most negative status effects and disables on initial cast.\\n\\nDISPEL TYPE: Strong Dispel",
