  "DOTA_Tooltip_ability_item_aegis_Description": "<h1>Passive: Reincarnation</h1> Brings you to life with full health and mana %reincarnate_time% seconds after you die, at the location where you died. <br><br>Reincarnation must be used within %disappear_time_minutes_tooltip% minutes or Aegis of the Immortal disappears. If it expires, it will heal you over 5 seconds (dispels on damage).",
  "DOTA_Tooltip_ability_item_aegis_Lore": "The Immortal was said to own a shield that protected him from death itself.",
  "DOTA_Tooltip_ability_item_aegis_Note0": "Pronounced as ayy jis.",
  "DOTA_Tooltip_ability_item_abyssal_blade_Description": "<h1>Active: Overwhelm</h1> Stuns a target enemy unit for %stun_duration% seconds. <br><br>Pierces Spell Immunity.<br><br>Range: %tooltip_range%\\n<h1>Passive: Bash</h1> Grants melee heroes a %bash_chance_melee%%% chance on hit to stun the target for %bash_duration% seconds and deal %bonus_chance_damage% bonus magical damage.  Bash chance for ranged heroes is %bash_chance_ranged%%%.\\n<h1>Passive: Damage Block</h1> Grants a %block_chance%%% chance to block %block_damage_melee% damage from incoming attacks on melee heroes, and %block_damage_ranged% damage on ranged.",
  "DOTA_Tooltip_ability_item_abyssal_blade_Lore": "The lost blade of the Commander of the Abyss, this edge cuts into an enemy's soul.",
  "DOTA_Tooltip_ability_item_abyssal_blade_Note0": "The stun is melee range.",
  "DOTA_Tooltip_ability_item_abyssal_blade_Note1": "Does not stack with other bashes.",
  "DOTA_Tooltip_ability_item_abyssal_blade_Note2": "The following heroes cannot trigger Bash on this item: Spirit Breaker, Faceless Void, Slardar, and Troll Warlord.",
  "DOTA_Tooltip_ability_item_abyssal_blade_Note3": "Multiple sources of damage block do not stack.",
  "DOTA_Tooltip_ability_item_abyssal_blade_bonus_damage": "+$damage",
  "DOTA_Tooltip_ability_item_abyssal_blade_bonus_health": "+$health",
  "DOTA_Tooltip_ability_item_abyssal_blade_bonus_health_regen": "+$hp_regen",
  "DOTA_Tooltip_ability_item_abyssal_blade_bonus_strength": "+$str",
  "DOTA_Tooltip_ability_item_arcane_ring_Description": "<h1>Use:</h1> Restores %replenish_amount% mana in an area.",
  "DOTA_Tooltip_ability_item_arcane_ring_bonus_armor": "+$armor",
  "DOTA_Tooltip_ability_item_arcane_ring_bonus_mana": "+$mana",
  "DOTA_Tooltip_ability_item_armlet_Description": "<h1>Toggle: Unholy Strength</h1>When active, Unholy Strength grants +%unholy_bonus_damage% damage, +%unholy_bonus_strength% strength and +%unholy_bonus_armor% armor, but drains %unholy_health_drain_per_second_tooltip% health per second.<br><br> You cannot die from the health drain when Unholy Strength is activated, nor from the strength loss when Unholy Strength is deactivated.",
  "DOTA_Tooltip_ability_item_armlet_Lore": "Weapon of choice among brutes, the bearer sacrifices his life energy to gain immense strength and power.",
  "DOTA_Tooltip_ability_item_armlet_Note0": "The strength change will affect both maximum and current HP, but you cannot die from the change.",
  "DOTA_Tooltip_ability_item_armlet_Note1": "The strength change occurs over 0.6 seconds.",
  "DOTA_Tooltip_ability_item_armlet_Note2": "Activating or deactivating Unholy Strength does not interrupt channeling.",
  "DOTA_Tooltip_ability_item_armlet_bonus_damage": "+$damage",
  "DOTA_Tooltip_ability_item_armlet_bonus_attack_speed": "+$attack",
  "DOTA_Tooltip_ability_item_armlet_bonus_armor": "+$armor",
  "DOTA_Tooltip_ability_item_armlet_bonus_health_regen": "+$hp_regen",
  "DOTA_Tooltip_ability_item_assault_Description": "<h1>Passive: Assault Aura</h1> Grants %aura_attack_speed% attack speed and %aura_positive_armor% armor to nearby allied units and structures, and decreases nearby enemy unit and structure armor by %aura_negative_armor%.<br><br>Radius: %aura_radius%",
  "DOTA_Tooltip_ability_item_assault_Note0": "Multiple instances of Assault Aura do not stack.",
  "DOTA_Tooltip_ability_item_assault_Lore": "Forged in the depths of the nether reaches, this hellish mail provides an army with increased armor and attack speed.",
  "DOTA_Tooltip_ability_item_assault_bonus_attack_speed": "+$attack",
  "DOTA_Tooltip_ability_item_assault_bonus_armor": "+$armor",
  "DOTA_Tooltip_ability_item_basher_Description": "<h1>Passive: Bash</h1> Grants melee heroes a %bash_chance_melee%%% chance on hit to stun the target for %bash_duration% seconds and deal %bonus_chance_damage% bonus magical damage.  Bash chance for ranged heroes is %bash_chance_ranged%%%.",
  "DOTA_Tooltip_ability_item_basher_Lore": "A feared weapon in the right hands, this maul's ability to shatter the defenses of its opponents should not be underestimated.",
  "DOTA_Tooltip_ability_item_basher_Note0": "Does not stack with other sources of Bash, however it will stack with Mini-Bash.",
  "DOTA_Tooltip_ability_item_basher_Note1": "The following heroes cannot trigger Bash on this item: Spirit Breaker, Faceless Void, Slardar, and Troll Warlord.",
  "DOTA_Tooltip_ability_item_basher_bonus_damage": "+$damage",
  "DOTA_Tooltip_ability_item_basher_bonus_strength": "+$str",
  "DOTA_Tooltip_ability_item_belt_of_strength_Lore": "A valued accessory for improving vitality.",
  "DOTA_Tooltip_ability_item_belt_of_strength_bonus_strength": "+$str",
  "DOTA_Tooltip_ability_item_bfury_Description": "<h1>Active: Chop Tree/Ward</h1> Destroy a target tree or ward.<br><br>Tree Cast Range: %cast_range_tooltip%<br>Ward Cast Range: %cast_range_ward% \\n<h1>Passive: Quell</h1> Increases attack damage against non-hero units by %tooltip_quelling_bonus%%% for melee heroes, and %tooltip_quelling_bonus_ranged%%% for ranged. \\n<h1>Passive: Cleave</h1> Deals %cleave_damage_percent%%% of attack damage as physical damage in a cone up to %cleave_distance% around the target. (Melee Only)",
  "DOTA_Tooltip_ability_item_bfury_Lore": "The bearer of this mighty axe gains the ability to cut down swaths of enemies at once.",
  "DOTA_Tooltip_ability_item_bfury_Note0": "Cleave damage is reduced by armor type but not by armor value.",
  "DOTA_Tooltip_ability_item_bfury_Note1": "Cleave damage goes through spell immunity.",
  "DOTA_Tooltip_ability_item_bfury_Note2": "If multiple sources of Cleave are present, each Cleave's damage is applied separately.",
  "DOTA_Tooltip_ability_item_bfury_Note3": "Chop can be used to destroy Techies' Remote Mines.",
  "DOTA_Tooltip_ability_item_bfury_bonus_damage": "+$damage",
  "DOTA_Tooltip_ability_item_bfury_bonus_health_regen": "+$hp_regen",
  "DOTA_Tooltip_ability_item_bfury_bonus_mana_regen": "+$mana_regen",
  "DOTA_Tooltip_ability_item_black_king_bar_Description": "<h1>Active: Avatar</h1> Grants Spell Immunity and 100%% Magic Damage Resistance.  Duration decreases with each use. <br><br>Duration: %duration% <br>Dispel Type: <span class=\\\"GameplayValues GameplayVariable\\\">Basic Dispel</span>",
  "DOTA_Tooltip_ability_item_black_king_bar_Lore": "A powerful staff imbued with the strength of giants.",
  "DOTA_Tooltip_ability_item_black_king_bar_Note0": "Purchasing another Black King Bar will not reset its immunity duration.",
  "DOTA_Tooltip_ability_item_black_king_bar_Note1": "Using Black King Bar may remove some positive buffs.",
  "DOTA_Tooltip_ability_item_black_king_bar_bonus_strength": "+$str",
  "DOTA_Tooltip_ability_item_black_king_bar_bonus_damage": "+$damage",
  "DOTA_Tooltip_ability_item_blade_mail_Description": "<h1>Active: Damage Return</h1>For %duration% seconds, duplicates any damage taken back to the unit that dealt the damage.",
  "DOTA_Tooltip_ability_item_blade_mail_Lore": "A razor-sharp coat of mail, it is the choice of selfless martyrs in combat.",
  "DOTA_Tooltip_ability_item_blade_mail_Note0": "Damage Return is calculated before any kind of reduction.",
  "DOTA_Tooltip_ability_item_blade_mail_Note1": "Damage Return doesn't reflect damage from other forms of Blade Mail.",
  "DOTA_Tooltip_ability_item_blade_mail_Note2": "Returned damage type is the same as it was received.",
  "DOTA_Tooltip_ability_item_blade_mail_Note3": "Can pierce Spell Immunity if the original damage type does.",
