  "DOTA_Tooltip_ability_item_blade_mail_bonus_armor": "+$armor",
  "DOTA_Tooltip_ability_item_blade_mail_bonus_intellect": "+$int",
  "DOTA_Tooltip_ability_item_blades_of_attack_Lore": "The damage of these small, concealable blades should not be underestimated.",
  "DOTA_Tooltip_ability_item_blades_of_attack_bonus_damage": "+$damage",
  "DOTA_Tooltip_ability_item_blade_of_alacrity_Lore": "A long blade imbued with time magic.",
  "DOTA_Tooltip_ability_item_blade_of_alacrity_bonus_agility": "+$agi",
  "DOTA_Tooltip_ability_item_blink_Description": "<h1>Active: Blink</h1> Teleport to a target point up to %blink_range% units away. <br><br>Blink Dagger cannot be used for %blink_damage_cooldown% seconds after taking damage from an enemy hero or Roshan.",
  "DOTA_Tooltip_ability_item_blink_Lore": "The fabled dagger used by the fastest assassin ever to walk the lands.",
  "DOTA_Tooltip_ability_item_blink_Note0": "Self-casting will cause you to teleport in the direction of your team's fountain.",
  "DOTA_Tooltip_ability_item_blink_Note1": "If you used Blink to teleport to a distance over the maximum range, you'll be teleported 4/5 of the maximum range instead.",
  "DOTA_Tooltip_ability_item_super_blink_Description": "<h1>Active: Blink</h1> Teleport to a target point up to %blink_range% units away.",
  "DOTA_Tooltip_ability_item_super_blink_Lore": "The fabled dagger used by the fastest assassin ever to walk the lands.",
  "DOTA_Tooltip_ability_item_bloodstone_Description": "<h1>Active: Pocket Deny</h1>Instantly causes you to die.<br><br>Must ground target to cast.  Cannot self cast.\\n<h1>Passive: Bloodpact</h1>Begins with %tooltip_charges% charges, and gains a charge each time an enemy hero dies within %charge_range% range. <br><br>Each charge grants %mana_per_charge% mana regeneration per second, and reduces respawn time by %respawn_time_reduction% seconds. <br><br>If the bearer dies, the Bloodstone loses a third of its charges.",
  "DOTA_Tooltip_ability_item_bloodstone_Lore": "The Bloodstone's bright ruby color is unmistakable on the battlefield, as the owner seems to have infinite vitality and spirit.",
  "DOTA_Tooltip_ability_item_bloodstone_Note0": "Only the first Bloodstone will gain charges.",
  "DOTA_Tooltip_ability_item_bloodstone_bonus_health": "+$health",
  "DOTA_Tooltip_ability_item_bloodstone_bonus_mana": "+$mana",
  "DOTA_Tooltip_ability_item_bloodstone_bonus_health_regen": "+$hp_regen",
  "DOTA_Tooltip_ability_item_bloodstone_bonus_mana_regen": "+$mana_regen",
  "DOTA_Tooltip_ability_item_boots_Description": "Flat movement speed bonuses from multiple pairs of boots do not stack.",
  "DOTA_Tooltip_ability_item_boots_Lore": "Fleet footwear, increasing movement.",
  "DOTA_Tooltip_ability_item_boots_bonus_movement_speed": "+$move_speed",
  "DOTA_Tooltip_ability_item_boots_of_elves_Lore": "A tensile fabric often used for its light weight and ease of movement.",
  "DOTA_Tooltip_ability_item_boots_of_elves_bonus_agility": "+$agi",
  "DOTA_Tooltip_ability_item_bottle_Description": "<h1>Active: Regenerate</h1>Consumes a charge to restore %health_restore% health and %mana_restore% mana over %restore_time% seconds. If the hero is attacked by an enemy hero or Roshan, the effect is lost.<br><br>The Bottle automatically refills at the fountain.<br><br>Hold Control to use on an allied hero.<br><br>Range: %tooltip_range%\\n<h1>Passive:  Store Rune</h1>Runes can be stored in the bottle for later use by right-clicking them. Unused runes will automatically activate after 2 minutes.<br><br>Using a stored rune fully refills the Bottle. Stored Bounty Runes replenish 2/3 charges.",
  "DOTA_Tooltip_ability_item_bottle_Lore": "An old bottle that survived the ages, the contents placed inside become enchanted.",
  "DOTA_Tooltip_ability_item_bottle_Note0": "Bottle is shareable. Stored runes cannot be shared.",
  "DOTA_Tooltip_ability_item_bottle_Note1": "If Bottle does not have full charges and is placed on a courier, the courier's movespeed will be slowed by 30%.",
  "DOTA_Tooltip_ability_item_bracer_Lore": "The bracer is a common choice to toughen up defenses and increase longevity.",
