  "DOTA_Tooltip_ability_item_bracer_bonus_agility": "+$agi",
  "DOTA_Tooltip_ability_item_bracer_bonus_intellect": "+$int",
  "DOTA_Tooltip_ability_item_branches_Description": "<h1>Use: Plant Tree</h1> Targets the ground to plant a happy little tree that lasts for %tree_duration% seconds.<br><br>Range: %range_tooltip%",
  "DOTA_Tooltip_ability_item_branches_Lore": "A seemingly ordinary branch, its ironlike qualities are bestowed upon the bearer.",
  "DOTA_Tooltip_ability_item_branches_bonus_all_stats": "+$all",
  "DOTA_Tooltip_ability_item_broadsword_Lore": "The classic weapon of choice for knights, this blade is sturdy and reliable for slaying enemies.",
  "DOTA_Tooltip_ability_item_broadsword_bonus_damage": "+$damage",
  "DOTA_Tooltip_ability_item_buckler_Description": "<h1>Active: Armor Bonus</h1> Gives +%bonus_aoe_armor% armor to all allied units.  Lasts %bonus_aoe_duration_hero% seconds on heroes, %bonus_aoe_duration% seconds on units.<br><br>Radius: %bonus_aoe_radius%",
  "DOTA_Tooltip_ability_item_buckler_Lore": "A powerful shield that imbues the bearer with the strength of heroes past, it is capable of protecting entire armies in battle.",
  "DOTA_Tooltip_ability_item_buckler_bonus_armor": "+$armor",
  "DOTA_Tooltip_ability_item_buckler_bonus_all_stats": "+$all",
  "DOTA_Tooltip_ability_item_butterfly_Description": "<h1>Active: Flutter</h1> Grants %bonus_move_speed%%% additional movement speed for %duration% seconds.",
  "DOTA_Tooltip_ability_item_butterfly_Note0": "Stacks diminishingly with other sources of Evasion.",
  "DOTA_Tooltip_ability_item_butterfly_Lore": "Only the mightiest and most experienced of warriors can wield the Butterfly, but it provides incredible dexterity in combat.",
