  "DOTA_Tooltip_ability_item_refresher_shard_Note0": "Refresher Shard is shareable.",
  "DOTA_Tooltip_ability_item_circlet_Lore": "An elegant circlet designed for human princesses.",
  "DOTA_Tooltip_ability_item_circlet_bonus_all_stats": "+$all",
  "DOTA_Tooltip_ability_item_clarity_Description": "<h1>Use: Replenish</h1> Grants %mana_regen% mana regeneration to the target for %buff_duration% seconds.<br><br>If the unit is attacked by an enemy hero or Roshan, the effect is lost.<br><br>Range: %range_tooltip%",
  "DOTA_Tooltip_ability_item_clarity_Lore": "Clear water that enhances the ability to meditate.",
  "DOTA_Tooltip_ability_item_faerie_fire_Description": "<h1>Use: Imbue</h1> Instantly restores %hp_restore% health.",
  "DOTA_Tooltip_ability_item_faerie_fire_bonus_damage": "+$damage",
  "DOTA_Tooltip_ability_item_faerie_fire_Lore": "The ethereal flames from the ever-burning ruins of Kindertree ignite across realities.",
  "DOTA_Tooltip_ability_item_infused_raindrop_Description": "<h1>Passive: Magical Damage Block</h1> Consumes a charge to block %magic_damage_block% magic damage from damage instances over %min_damage% damage. <br><br>Comes with %initial_charges% charges. When the charges are gone, the item disappears.",
