  "DOTA_Tooltip_ability_item_infused_raindrop_Lore": "Elemental protection from magical assaults.",
  "DOTA_Tooltip_ability_item_bloodthorn_Description": "<h1>Active: Soul Rend</h1> Silences a target for %silence_duration% seconds. At the end of the silence, an additional %silence_damage_percent%%% of all damage taken during the silence will be dealt to the target as magical damage.<br><br>All attacks on the silenced target will have True Strike and %tooltip_crit_chance%%% chance to crit for %target_crit_multiplier%%% damage.<br><br>Range: %tooltip_range%\\n<h1>Passive: Critical Strike</h1> Grants a %crit_chance%%% chance for attacks to inflict %crit_multiplier%%% damage.",
  "DOTA_Tooltip_ability_item_bloodthorn_Note0": "Critical Strike does not work against buildings.",
  "DOTA_Tooltip_ability_item_bloodthorn_Lore": "A reviled blade that bites deeper with each wriggle of its victim's final throes.",
  "DOTA_Tooltip_ability_item_bloodthorn_bonus_intellect": "+$int",
  "DOTA_Tooltip_ability_item_bloodthorn_bonus_attack_speed": "+$attack",
  "DOTA_Tooltip_ability_item_bloodthorn_bonus_damage": "+$damage",
  "DOTA_Tooltip_ability_item_bloodthorn_bonus_mana_regen": "+$mana_regen",
  "DOTA_Tooltip_ability_item_dragon_lance_Description": "<h1>Passive: Dragon's Reach</h1> Increases attack range of ranged heroes by %base_attack_range%.",
  "DOTA_Tooltip_ability_item_dragon_lance_Note0": "Passive does not stack.",
  "DOTA_Tooltip_ability_item_dragon_lance_bonus_agility": "+$agi",
  "DOTA_Tooltip_ability_item_dragon_lance_bonus_strength": "+$str",
  "DOTA_Tooltip_ability_item_dragon_lance_Lore": "The forward charge of the wyvern host grants no quarter.",
  "DOTA_Tooltip_ability_item_aether_lens_Description": "<h1>Passive: Aethereal Focus</h1> Increases targeted spell and item cast range by %cast_range_bonus%.",
  "DOTA_Tooltip_ability_item_aether_lens_Note0": "Passive does not stack.",
  "DOTA_Tooltip_ability_item_aether_lens_bonus_mana": "+$mana",
  "DOTA_Tooltip_ability_item_aether_lens_bonus_mana_regen": "+$mana_regen",
  "DOTA_Tooltip_ability_item_aether_lens_Lore": "Polished with the incantation of his final breath, the gift of a dying mage to his sickly son.",
  "DOTA_Tooltip_ability_item_claymore_Lore": "A sword that can cut through armor, it's a commonly chosen first weapon for budding swordsmen.",
  "DOTA_Tooltip_ability_item_claymore_bonus_damage": "+$damage",
  "DOTA_Tooltip_ability_item_cloak_Lore": "A cloak made of a magical material that works to dispel any magic cast on it.",
  "DOTA_Tooltip_ability_item_cloak_Note0": "Stacks multiplicatively with other sources of spell resistance.",
  "DOTA_Tooltip_ability_item_cloak_tooltip_resist": "%+$spell_resist",
  "DOTA_Tooltip_ability_item_courier_Description": "<h1>Use: Deploy Courier</h1> Deploys a creature to carry items to and from your team's base.",
  "DOTA_Tooltip_ability_item_courier_Lore": "Losing the courier is punishable by death.",
  "DOTA_Tooltip_ability_item_flying_courier_Description": "<h1>Use: Upgrade Courier</h1>Upgrades your team's Animal Courier to a Flying Courier, granting it swift, unobstructed movement to carry items to and from your team's base. <br><br>Requires a deployed Animal Courier.",
  "DOTA_Tooltip_ability_item_flying_courier_Lore": "The faithful mount upon which soar the fortunes of battle.",
  "DOTA_Tooltip_ability_item_cyclone_Description": "<h1>Active: Cyclone</h1> Sweeps a target unit up into a cyclone, making them invulnerable for %cyclone_duration% seconds. Cyclone can only be cast on enemy units or yourself.<br><br>Enemy units take %tooltip_drop_damage% magical damage upon landing.<br><br>Range: %cast_range_tooltip%<br>Dispel Type: <span class=\\\"GameplayValues GameplayVariable\\\">Basic Dispel</span>",
  "DOTA_Tooltip_ability_item_cyclone_Lore": "A mysterious scepter passed down through the ages, its disruptive winds can be used for good or evil.",
