  "DOTA_Tooltip_ability_item_cyclone_bonus_mana_regen": "+$mana_regen",
  "DOTA_Tooltip_ability_item_cyclone_bonus_movement_speed": "+$move_speed",
  "DOTA_Tooltip_ability_item_cyclone_2_Description": "<h1>Active: Cyclone</h1> A target unit is swept up in a cyclone and made invulnerable for %cyclone_duration% seconds. Cyclone can be cast on yourself. Enemy units take 50 magical damage upon landing.",
  "DOTA_Tooltip_ability_item_cyclone_2_Lore": "A mysterious scepter passed down through the ages, its disruptive winds can be used for good or evil.",
  "DOTA_Tooltip_ability_item_cyclone_2_Note0": "You cannot cyclone allies.",
  "DOTA_Tooltip_ability_item_cyclone_2_Note1": "Cyclones cast on yourself go through spell immunity.",
  "DOTA_Tooltip_ability_item_cyclone_2_bonus_intellect": "+$int",
  "DOTA_Tooltip_ability_item_cyclone_2_bonus_mana_regen": "+$mana_regen",
  "DOTA_Tooltip_ability_item_cyclone_2_bonus_movement_speed": "+$move_speed",
  "DOTA_Tooltip_ability_item_dagon_Description": "<h1>Active: Energy Burst</h1> Emits a powerful burst of magical damage upon a targeted enemy unit. Upgradable.<br><br>Damage: %damage%<br>Range: %range_tooltip%<br>Mana Cost: %mana_cost_tooltip%",
  "DOTA_Tooltip_ability_item_dagon_Lore": "A lesser wand that grows in power the longer it is used, it brings magic to the fingertips of the user.",
