  "DOTA_Tooltip_ability_item_dagon_4_Description": "<h1>Active: Energy Burst</h1> Emits a powerful burst of magical damage upon a targeted enemy unit. Upgradable.<br><br>Damage: %damage%<br>Range: %range_tooltip%<br>Mana Cost: %mana_cost_tooltip%",
  "DOTA_Tooltip_ability_item_dagon_4_Lore": "A lesser wand that grows in power the longer it is used, it brings magic to the fingertips of the user.",
