  "DOTA_Tooltip_ability_item_rod_of_atos_Description": "<h1>Active: Cripple</h1>Roots the target for %duration% seconds.<br><br>Range: %tooltip_range%",
  "DOTA_Tooltip_ability_item_rod_of_atos_Lore": "Atos, the Lord of Blight, has his essence stored in this deceptively simple wand.",
