  "DOTA_Tooltip_ability_item_rod_of_atos_bonus_agility": "+$agi",
  "DOTA_Tooltip_ability_item_sange_Description": "<h1>Passive: Maim</h1>Each attack has a %maim_chance%%% chance to reduce enemy hero movement speed by %maim_slow_movement%%% and attack speed by %maim_slow_attack% when used by a melee hero. Reduces enemy hero movement by %maim_slow_movement_range%%% and attack speed by %maim_slow_attack_range% when used by a ranged hero. Effect lasts %maim_duration% seconds.",
  "DOTA_Tooltip_ability_item_sange_Lore": "Sange is an unusually accurate weapon, seeking weak points automatically.",
  "DOTA_Tooltip_ability_item_sange_bonus_damage": "+$damage",
  "DOTA_Tooltip_ability_item_sange_bonus_strength": "+$str",
  "DOTA_Tooltip_ability_item_heavens_halberd_Description": "<h1>Active: Disarm</h1>Prevents a target from attacking for %disarm_melee% seconds on melee targets, and %disarm_range% seconds on ranged targets.<br><br>Range: %disarm_cast_range_tooltip% \\n<h1>Passive: Maim</h1>Each attack has a %maim_chance%%% chance to reduce enemy hero movement speed by %maim_slow_movement%%% and attack speed by %maim_slow_attack% if the user is melee. Reduces enemy hero movement by %maim_slow_movement_range%%% and attack speed by %maim_slow_attack_range% if the user is ranged. Effect lasts %maim_duration% seconds.",
  "DOTA_Tooltip_ability_item_heavens_halberd_Lore": "This halberd moves with the speed of a smaller weapon, allowing the bearer to win duels that a heavy edge would not.",
  "DOTA_Tooltip_ability_item_heavens_halberd_bonus_evasion": "%+$evasion",
  "DOTA_Tooltip_ability_item_heavens_halberd_bonus_damage": "+$damage",
  "DOTA_Tooltip_ability_item_heavens_halberd_bonus_strength": "+$str",
  "DOTA_Tooltip_ability_item_sange_and_yasha_Description": "<h1>Passive: Maim</h1>Each attack has a %maim_chance%%% chance to reduce enemy hero movement speed by %maim_slow_movement%%% and attack speed by %maim_slow_attack% when used by a melee hero. Reduces enemy hero movement by %maim_slow_movement_range%%% and attack speed by %maim_slow_attack_range% when used by a ranged hero. Effect lasts %maim_duration% seconds.",
  "DOTA_Tooltip_ability_item_sange_and_yasha_Note0": "Yasha-based movement speed bonuses from multiple items do not stack.",
  "DOTA_Tooltip_ability_item_sange_and_yasha_Lore": "Sange and Yasha, when attuned by the moonlight and used together, become a very powerful combination.",
