  "DOTA_Tooltip_ability_item_sange_and_yasha_bonus_strength": "+$str",
  "DOTA_Tooltip_ability_item_sange_and_yasha_bonus_agility": "+$agi",
  "DOTA_Tooltip_ability_item_sange_and_yasha_bonus_attack_speed": "+$attack",
  "DOTA_Tooltip_ability_item_sange_and_yasha_movement_speed_percent_bonus": "%+$move_speed",
  "DOTA_Tooltip_ability_item_satanic_Description": "<h1>Active: Unholy Rage</h1>Increases Lifesteal percentage to %unholy_lifesteal_total_tooltip%%% for %unholy_duration% seconds. \\n<h1>Passive: Lifesteal</h1>Heals the attacker for %lifesteal_percent%%% of attack damage dealt.",
  "DOTA_Tooltip_ability_item_satanic_Lore": "Immense power at the cost of your soul.",
  "DOTA_Tooltip_ability_item_satanic_bonus_strength": "+$str",
  "DOTA_Tooltip_ability_item_satanic_bonus_damage": "+$damage",
  "DOTA_Tooltip_ability_item_sheepstick_Description": "<h1>Active: Hex</h1>Turns a target unit into a harmless critter for %sheep_duration% seconds. The target has a base movement speed of 140 and will be silenced, muted, and disarmed.<br>Instantly destroys illusions.<br><br>Range: %tooltip_range%",
  "DOTA_Tooltip_ability_item_sheepstick_Note0": "The target will have a base movement speed of 140, but buffs granting maximum movement speed won't be disabled.",
  "DOTA_Tooltip_ability_item_sheepstick_Lore": "The most guarded relic among the cult of Vyse, it is the most coveted weapon among magi.",
  "DOTA_Tooltip_ability_item_sheepstick_bonus_strength": "+$str",
  "DOTA_Tooltip_ability_item_sheepstick_bonus_agility": "+$agi",
  "DOTA_Tooltip_ability_item_sheepstick_bonus_intellect": "+$int",
  "DOTA_Tooltip_ability_item_sheepstick_bonus_mana_regen": "+$mana_regen",
  "DOTA_Tooltip_ability_item_shivas_guard_Description": "<h1>Active: Arctic Blast</h1> Emits a freezing wave that deals %blast_damage% magical damage to enemies and slows their movement by %blast_movement_speed%%% for %blast_debuff_duration% seconds.<br><br>Radius: %blast_radius%\\n<h1>Passive: Freezing Aura</h1> Reduces the attack speed of all enemies by %aura_attack_speed%. <br><br>Radius: %aura_radius%",
  "DOTA_Tooltip_ability_item_shivas_guard_Lore": "Said to have belonged to a goddess, today it retains much of its former power.",
  "DOTA_Tooltip_ability_item_shivas_guard_Note0": "The wave extends at a speed of 350 to a max size of 900.",
  "DOTA_Tooltip_ability_item_shivas_guard_Note1": "The Arctic Blast follows its caster.",
  "DOTA_Tooltip_ability_item_shivas_guard_Note2": "Multiple instances of Freezing Aura do not stack.",
  "DOTA_Tooltip_ability_item_shivas_guard_bonus_intellect": "+$int",
  "DOTA_Tooltip_ability_item_shivas_guard_bonus_armor": "+$armor",
  "DOTA_Tooltip_ability_item_skadi_Description": "<h1>Passive: Cold Attack</h1> Attacks lower enemy attack speed by %cold_attack_speed% and movement speed by %cold_movement_speed%%%. <br><br>Lasts %cold_duration_melee% seconds when used by melee heroes, and %cold_duration_ranged% seconds when used by ranged.",
  "DOTA_Tooltip_ability_item_skadi_Lore": "Extremely rare artifact, guarded by the azure dragons.",
  "DOTA_Tooltip_ability_item_skadi_Note0": "Lasts 5 seconds with melee Eye of Skadi, 2.5 seconds with ranged Eye of Skadi.",
