  "DOTA_Tooltip_ability_item_soul_booster_bonus_health": "+$health",
  "DOTA_Tooltip_ability_item_soul_booster_bonus_mana": "+$mana",
  "DOTA_Tooltip_ability_item_soul_ring_Description": "<h1>Active: Sacrifice</h1> Consume %health_sacrifice% health to temporarily gain %mana_gain% mana.  Lasts %duration% seconds.<br><br>If the mana gained cannot fit in your mana pool, it creates a buffer of mana that will be used before your mana pool.",
  "DOTA_Tooltip_ability_item_soul_ring_Lore": "A ring that feeds on the souls of those who wear it.",
