  "DOTA_Tooltip_ability_item_staff_of_wizardry_Lore": "A staff of magical powers passed down from the eldest mages.",
  "DOTA_Tooltip_ability_item_staff_of_wizardry_bonus_intellect": "+$int",
  "DOTA_Tooltip_ability_item_stout_shield_Description": "<h1>Passive: Damage Block</h1>Grants a %block_chance%%% chance to block %damage_block_melee% damage from incoming attacks on melee heroes, and %damage_block_ranged% damage on ranged.",
  "DOTA_Tooltip_ability_item_stout_shield_Lore": "One man's wine barrel bottom is another man's shield.",
