  "DOTA_Tooltip_ability_item_talisman_of_evasion_Note0": "Stacks diminishingly with other sources of Evasion.",
  "DOTA_Tooltip_ability_item_talisman_of_evasion_Lore": "A necklace that allows you to anticipate enemy attacks.",
  "DOTA_Tooltip_ability_item_talisman_of_evasion_bonus_evasion": "%+$evasion",
  "DOTA_Tooltip_ability_item_tango_Description": "<h1>Use: Devour</h1> Consumes a target tree to gain %health_regen% health regeneration for %buff_duration% seconds. Consuming a ward or Ironwood Tree doubles the heal amount.<br><br>Comes with %tooltip_charges% charges.  Can be used on an allied hero to give them one Tango.<br><br>Range: %tooltip_range%<br>Ward Range: %cast_range_ward%",
  "DOTA_Tooltip_ability_item_tango_Lore": "Forage to survive on the battlefield.",
  "DOTA_Tooltip_ability_item_tango_single_Description": "<h1>Use: Devour</h1> Consumes a target tree to gain %health_regen% health regeneration for %buff_duration% seconds. Consuming a ward or Ironwood Tree doubles the heal amount.<br><br>Range: %tooltip_range%<br>Ward Range: %cast_range_ward%",
  "DOTA_Tooltip_ability_item_tango_single_Lore": "Om nom nom.",
  "DOTA_Tooltip_ability_item_tpscroll_Description": "<h1>Use: Teleport</h1>After channeling for %tooltip_channel_time% seconds, teleports you to a target friendly building. <br><br>Double-click to teleport to your team's base fountain.",
  "DOTA_Tooltip_ability_item_tpscroll_Note0": "If multiple heroes teleport to the same location in succession, the channeling time will be increased for each successive hero.",
  "DOTA_Tooltip_ability_item_tpscroll_Lore": "What a hero truly needs.",
  "DOTA_Tooltip_ability_item_tranquil_boots_Description": "<h1>Passive: Break</h1> Whenever you attack or are attacked, the bonus %bonus_health_regen% HP regen is lost and the movement speed bonus is reduced to %broken_movement_speed% for %break_time% seconds.\\nFlat movement speed bonuses from multiple pairs of boots do not stack.",
  "DOTA_Tooltip_ability_item_tranquil_boots_Lore": "While they increase the longevity of the wearer, this boot is not particularly reliable.",
  "DOTA_Tooltip_ability_item_tranquil_boots_bonus_movement_speed": "+$move_speed",
  "DOTA_Tooltip_ability_item_tranquil_boots_bonus_health_regen": "+$hp_regen",
  "DOTA_Tooltip_ability_item_tranquil_boots2_Description": "<h1>Passive: Break</h1> Whenever you attack or are attacked, the bonus %bonus_health_health% HP regen is lost and your movement speed is reduced to %broken_movement_speed% for %break_time% seconds.",
  "DOTA_Tooltip_ability_item_tranquil_boots2_Lore": "While they increase the longevity of the wearer, this boot is not particularly reliable.",
  "DOTA_Tooltip_ability_item_tranquil_boots2_bonus_movement_speed": "+$move_speed",
  "DOTA_Tooltip_ability_item_tranquil_boots2_bonus_armor": "+$armor",
  "DOTA_Tooltip_ability_item_tranquil_boots2_bonus_health_regen": "+$hp_regen",
  "DOTA_Tooltip_ability_item_travel_boots_Description": "<h1>Active: Teleport</h1>Teleports you to an allied non-hero unit or structure. Teleporting to a unit is interrupted if the target unit dies.<br><br>Shares a cooldown with Town Portal Scroll.\\nFlat movement speed bonuses from multiple pairs of boots do not stack.",
  "DOTA_Tooltip_ability_item_travel_boots_Lore": "Winged boots that grant omnipresence.",
  "DOTA_Tooltip_ability_item_travel_boots_bonus_movement_speed": "+$move_speed",
  "DOTA_Tooltip_ability_item_travel_boots_2_Description": "<h1>Active: Teleport</h1> Teleports you to any allied structure or unit, including heroes. Teleporting to a unit is interrupted if the target unit dies.<br><br>Shares a cooldown with Town Portal Scroll.\\nFlat movement speed bonuses from multiple pairs of boots do not stack.",
  "DOTA_Tooltip_ability_item_travel_boots_2_Lore": "Winged boots that grant omnipresence.",
  "DOTA_Tooltip_ability_item_travel_boots_2_bonus_movement_speed": "+$move_speed",
