  "DOTA_Tooltip_ability_item_ultimate_scepter_bonus_health": "+$health",
  "DOTA_Tooltip_ability_item_ultimate_scepter_bonus_mana": "+$mana",
  "DOTA_Tooltip_ability_item_urn_of_shadows_Description": "<h1>Active: Soul Release</h1> Provides %soul_heal_amount% health regeneration when cast on allies, and deals %soul_damage_amount% damage per second when cast on enemies. <br><br>Lasts %duration% seconds.  <br><br>Gains charges every time an enemy hero dies within %soul_radius% units.  Only the closest Urn to the dying hero will gain a charge.<br><br>Cast Range: %soul_release_range_tooltip%",
  "DOTA_Tooltip_ability_item_urn_of_shadows_Note0": "Empty urns gain 2 charges.",
  "DOTA_Tooltip_ability_item_urn_of_shadows_Note1": "If used on a hero with Soul Release already active on them, it will refresh its duration.",
  "DOTA_Tooltip_ability_item_urn_of_shadows_Lore": "Contains the ashes of powerful demons.",
  "DOTA_Tooltip_ability_item_urn_of_shadows_mana_regen": "+$mana_regen",
  "DOTA_Tooltip_ability_item_urn_of_shadows_bonus_all_stats": "+$all",
  "DOTA_Tooltip_ability_item_urn_of_shadows_bonus_armor": "+$armor",
  "DOTA_Tooltip_ability_item_vanguard_Description": "<h1>Passive: Damage Block</h1> Grants a %block_chance%%% chance to block %block_damage_melee% damage from incoming attacks on melee heroes, and %block_damage_ranged% damage on ranged.",
