  "DOTA_Tooltip_ability_item_vanguard_Note0": "Multiple sources of damage block do not stack.",
  "DOTA_Tooltip_ability_item_vanguard_bonus_health": "+$health",
  "DOTA_Tooltip_ability_item_vanguard_bonus_health_regen": "+$hp_regen",
  "DOTA_Tooltip_ability_item_crimson_guard_Description": "<h1>Active: Guard</h1> For %duration% seconds, grant nearby allied heroes and buildings +%bonus_aoe_armor% armor, and a %block_chance_active%%% chance to block %block_damage_melee_active% damage from each incoming attack.<br><br>Units may only be affected by Guard once every %tooltip_reapply_time% seconds.<br><br>Radius: %bonus_aoe_radius%\\n<h1>Passive: Damage Block</h1> Grants a %block_chance%%% chance to block %block_damage_melee% damage from incoming attacks on melee heroes, and %block_damage_ranged% damage on ranged.",
