  "DOTA_Tooltip_ability_item_vitality_booster_bonus_health": "+$health",
  "DOTA_Tooltip_ability_item_vladmir_Description": "<h1>Passive: Vladmir's Aura</h1>Grants lifesteal (%vampiric_aura%%% for melee heroes, %vampiric_aura_ranged%%% for ranged), %hp_regen% health regeneration, %mana_regen_aura% mana regeneration, %damage_aura%%% bonus attack damage, and %armor_aura% armor to nearby allies. <br><br>Radius: %aura_radius%",
  "DOTA_Tooltip_ability_item_vladmir_Lore": "An eerie mask that is haunted with the malice of a fallen vampire.",
  "DOTA_Tooltip_ability_item_vladmir_Note0": "Multiple instances of Vladmir's Aura do not stack.",
  "DOTA_Tooltip_ability_item_vladmir_bonus_all_stats": "+$all",
  "DOTA_Tooltip_ability_item_void_stone_Description": 0,
  "DOTA_Tooltip_ability_item_void_stone_Lore": "Jewelry that was once used to channel nether realm magic, this ring pulses with energy.",
  "DOTA_Tooltip_ability_item_void_stone_Note0": "Void Stone is shareable.",
  "DOTA_Tooltip_ability_item_void_stone_bonus_mana_regen": "+$mana_regen",
  "DOTA_Tooltip_ability_item_ward_observer_Description": "<h1>Use: Plant</h1>Plants an Observer Ward, an invisible watcher that gives ground vision in a %vision_range% radius to your team. Lasts %duration_minutes_tooltip% minutes.<br><br>Hold Control to give one Observer Ward to an allied hero.<br><br>Range: %tooltip_range%",
  "DOTA_Tooltip_ability_item_ward_observer_Lore": "A form of half-sentient plant, often cultivated by apprentice wizards.",
  "DOTA_Tooltip_ability_item_ward_sentry_Description": "<h1>Use: Plant</h1> Plants a Sentry Ward, an invisible watcher that grants True Sight, the ability to see invisible enemy units and wards, to any existing allied vision within a %true_sight_range% radius.<br>Lasts %duration_minutes_tooltip% minutes.<br><br>Does not grant ground vision.<br>Hold Control to give one Sentry Ward to an allied hero.<br><br>Range: %tooltip_range%",
  "DOTA_Tooltip_ability_item_ward_sentry_Lore": "A form of plant originally grown in the garden of a fearful king.",
  "DOTA_Tooltip_ability_item_ward_dispenser_Description": "<h1>Use: Plant</h1> Plant the currently active ward.  Double-Click to switch the currently active ward.<br><br>Range: %tooltip_range%",
  "DOTA_Tooltip_ability_item_ward_dispenser_Note0": "Hold Control to give one ward to an allied hero.",
  "DOTA_Tooltip_ability_item_ward_dispenser_observer_vision_range_tooltip": "OBSERVER VISION RANGE:",
