  "DOTA_Tooltip_ability_item_wraith_band_bonus_intellect": "+$int",
  "DOTA_Tooltip_ability_item_yasha_Lore": "Yasha is regarded as the swiftest weapon ever created.",
  "DOTA_Tooltip_ability_item_yasha_Note0": "Yasha-based movement speed bonuses from multiple items do not stack.",
  "DOTA_Tooltip_ability_item_yasha_bonus_agility": "+$agi",
