  "DOTA_Tooltip_ability_item_yasha_movement_speed_percent_bonus": "%+$move_speed",
  "DOTA_Tooltip_ability_item_arcane_boots_Description": "<h1>Active: Replenish Mana</h1>Restores %replenish_amount% mana to all nearby allies.<br><br>Radius: %replenish_radius%\\nFlat movement speed bonuses from multiple pairs of boots do not stack.",
