  "DOTA_Tooltip_ability_item_arcane_boots_bonus_mana": "+$mana",
  "DOTA_Tooltip_ability_item_orb_of_venom_Description": "<h1>Passive: Poison Attack</h1>Poisons the target, dealing %poison_damage_melee% magical damage per second and slowing movement by %poison_movement_speed_melee%%% if the equipped hero is melee, or %poison_damage_range% magical damage per second and slowing movement by %poison_movement_speed_range%%% if they are ranged. Lasts for %poison_duration% seconds.",
  "DOTA_Tooltip_ability_item_orb_of_venom_Lore": "Envenoms your veapon with the venom of a venomous viper.",
